.programs-page {
	background-color : $primary;
	padding-bottom   : 100px;
	padding-top      : 86px;


	@media screen and (min-width : $break-table) {
		padding-bottom : 0;
		padding-top    : 132px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 80px;
		padding-top    : 240px;
	}
}

.programs-page__container {
	position : relative;

	.programs-page__circle {
		height   : 30px;
		left     : -18px;
		position : absolute;
		top      : -13px;
		width    : 30px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			height : auto;
			left   : -24px;
			top    : -26px;
			width  : auto;
		}

		@media screen and (min-width : $break-desktop) {
			left : -64px;
			top  : 52px;
		}


	}

	.programs-page__dots {
		left     : -500px;
		position : absolute;
		top      : 410px;
		z-index  : 1;

		&--middle {
			bottom  : 588px;
			left    : -450px;
			top     : unset;
			z-index : 1;
		}

		&--top {
			bottom  : unset;
			height  : 120px;
			left    : unset;
			right   : -67px;
			top     : 52px;
			width   : 120px;
			z-index : 1;
			@media screen and (min-width : $break-table) {
				height : 246px;
				right  : -217px;
				top    : -1px;
				width  : 316px;
			}

			@media screen and (min-width : $break-desktop) {
				right : -531px;
				top   : -105px;
			}
		}
	}

	.programs-page__rectangle {
		left     : -190px;
		position : absolute;
		top      : 1141px;
		z-index  : 1;

		&--right {
			bottom : 276px;
			left   : unset;
			right  : -251px;
			top    : unset;
		}
	}

	.programs-page__rhombus {
		bottom   : 1244px;
		left     : -232px;
		position : absolute;
		z-index  : 1;

		&--right {
			bottom : unset;
			left   : unset;
			right  : -238px;
			top    : 710px;
		}
	}

	.programs-page__wave {
		bottom   : 0;
		left     : -236px;
		position : absolute;
		z-index  : 1;

		&--right {
			bottom : 913px;
			left   : unset;
			right  : -294px;
		}
	}

	.programs-page__lines {
		height   : 244px;
		position : absolute;
		right    : -417px;
		top      : 1616px;
		width    : 244px;
		z-index  : 1;
	}
}


.programs-banner--programs {
	margin-bottom : 58px;
	max-width     : 253px;
	position      : relative;
	z-index       : 2;

	@media screen and (min-width : $break-table) {
		margin-bottom : 92px;
		max-width     : 474px;
		padding       : 36px 36px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 119px;
		max-width     : none;
	}

	.programs-banner__right {
		display  : none;
		position : relative;
		right    : -51px;
		top      : 0;

		img {
			height    : 407px;
			max-width : none;
			width     : 449px;
		}

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : block;
		}
	}
}

.select-wrapper {
	align-items     : center;
	display         : flex;
	flex-wrap       : wrap;
	gap             : 12px;
	justify-content : flex-start;
	margin-bottom   : 36px;
	position        : relative;
	width           : 100%;
	z-index         : 20;


	@media screen and (min-width : $break-table) {
		gap           : 16px;
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		gap           : 24px;
		margin-bottom : 80px;
	}

}


.programs-page__group {
	&:not(:last-child) {
		margin-bottom : 46px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 64px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 80px;
		}
	}
}

.programs-page__category {
	color         : $black;
	font-size     : 16px;
	font-weight   : 800;
	line-height   : 24px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 32px;
		line-height   : 42px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 36px;
	}
}

.programs-page__grid {
	display               : grid;
	grid-column-gap       : 12px;
	grid-row-gap          : 12px;
	grid-template-columns : repeat(6, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap : 24px;
		grid-row-gap    : 29px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 29px;
		grid-row-gap          : 36px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.programs-page__item {
	align-items   : inherit;
	border-radius : 12px;
	display       : flex;
	grid-column   : 3 span;
	overflow      : hidden;
	padding       : 16px 16px 27px 16px;
	position      : relative;
	transition    : background-color .3s ease, color .3s ease;
	width         : 100%;

	@media screen and (min-width : $break-table) {
		padding : 24px 24px 35px 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&.disabled {

		opacity        : 0.6;
		pointer-events : none;

	}

	&:hover {
		background-color : $white;
	}

	&--lightblue {
		background-color : $light-blue;

		&:hover {
			.programs-page__box {
				background-color : $light-blue;
				transition       : background-color .3s ease;
			}
		}

		.dots {
			height   : 70px;
			position : absolute;
			right    : -15px;
			top      : -22px;
			width    : 70px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				height : 144px;
				right  : -24px;
				top    : -72px;
				width  : 144px;
			}

			@media screen and (min-width : $break-desktop) {
				right : -72px;
			}
		}

		.wave {
			height    : 13px;
			left      : -15px;
			position  : absolute;
			top       : 8px;
			transform : rotate(90deg);
			width     : 60px;
			z-index   : 1;

			@media screen and (min-width : $break-table) {
				height : auto;
				left   : -36px;
				top    : -7px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {
			}
		}

		.rhombus {
			bottom   : -9px;
			height   : 20px;
			left     : 87px;
			position : absolute;
			width    : 20px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom    : -41px;
				height    : auto;
				left      : 50%;
				transform : translateY(-50%);
				width     : auto;
			}

			@media screen and (min-width : $break-desktop) {
			}
		}
	}

	&--blue {
		background-color : $blue;

		&:hover {
			.programs-page__box {
				background-color : $blue;
				color            : $white;
				transition       : background-color .3s ease;
			}
		}

		.dots {
			bottom   : -22px;
			height   : 70px;
			left     : -23px;
			position : absolute;
			width    : 70px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom : -76px;
				height : 144px;
				left   : -56px;
				width  : 144px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.wave {
			bottom    : 17px;
			height    : 13px;
			left      : 59px;
			position  : absolute;
			transform : rotate(90deg);
			width     : 60px;
			z-index   : 1;

			@media screen and (min-width : $break-table) {
				bottom : 19px;
				height : auto;
				left   : 133px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {
			}
		}

		.rhombus {
			height   : 20px;
			position : absolute;
			right    : 26px;
			top      : 5px;
			width    : 20px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				height : auto;
				right  : 58px;
				top    : 8px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {
			}
		}


	}

	&--green {
		background-color : $green;

		&:hover {
			.programs-page__box {
				background-color : $green;
				transition       : background-color .3s ease;
			}
		}

		.lines {
			height   : 70px;
			position : absolute;
			right    : -29px;
			top      : -15px;
			width    : 70px;

			@media screen and (min-width : $break-table) {
				height : 170px;
				right  : -12px;
				top    : -52px;
				width  : 170px;
			}

			@media screen and (min-width : $break-desktop) {
				right : -82px;
				top   : -84px;
			}
		}

		.line {
			bottom    : -3px;
			height    : 10px;
			left      : -24px;
			position  : absolute;
			transform : rotate(90deg);
			width     : 63px;

			@media screen and (min-width : $break-table) {
				bottom : -24px;
				height : 22px;
				left   : -68px;
				width  : 144px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.circle {
			bottom   : -13px;
			height   : 24px;
			position : absolute;
			right    : 41px;
			width    : 24px;

			@media screen and (min-width : $break-table) {
				bottom : -35px;
				height : 52px;
				left   : 89px;
				right  : unset;
				width  : 52px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	&--pink {
		background-color : $pink;

		&:hover {
			.programs-page__box {
				background-color : $pink;
				transition       : background-color .3s ease;
			}
		}

		.dots {
			height   : 70px;
			left     : -15px;
			position : absolute;
			top      : -22px;
			width    : 70px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				height : 144px;
				left   : -24px;
				top    : -72px;
				width  : 144px;
			}

			@media screen and (min-width : $break-desktop) {
				left : -72px;
			}
		}

		.wave {
			height    : 13px;
			position  : absolute;
			right     : 1px;
			top       : 8px;
			transform : rotate(90deg);
			width     : 60px;
			z-index   : 1;

			@media screen and (min-width : $break-table) {
				height : auto;
				right  : 21px;
				top    : -5px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {
			}
		}

		.rhombus {
			bottom   : -9px;
			height   : 20px;
			position : absolute;
			right    : 56px;
			width    : 20px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom : -20px;
				height : auto;
				right  : 123px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {
				right : 75px;
			}
		}
	}

}


.programs-page__box {
	align-items      : center;
	background-color : $white;
	color            : $black;
	display          : flex;
	font-size        : 14px;
	font-weight      : 800;
	line-height      : 20px;
	min-height       : 56px;
	position         : relative;
	transition       : background-color .3s ease;
	width            : 100%;
	z-index          : 2;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 32px;
		min-height  : 80px;
		span {
			max-width : 251px;
		}
	}

	@media screen and (min-width : $break-desktop) {

	}
}
