.readalso {
	background-color : $primary;
	padding          : 64px 0;

	@media screen and (min-width : $break-table) {
		padding : 96px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}
}

.readalso__container {
	position : relative;
}

.readalso__circle {
	bottom   : -50px;
	height   : 33px;
	position : absolute;
	right    : -17px;
	width    : 33px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 77px;
		height : auto;
		right  : -22px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 330px;
		right  : -211px;
	}

}

.readalso__wave {
	height   : 17px;
	position : absolute;
	right    : -15px;
	top      : -9px;
	width    : 80px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : auto;
		right  : 144px;
		top    : 32px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {
		right : 333px;
		top   : 0;
	}
}

.readalso__dots {
	left     : -610px;
	position : absolute;
	top      : 0;
	z-index  : 1;
}

.readalso__rhombus {
	bottom   : -52px;
	left     : -10px;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		bottom : 181px;
		left   : -199px;
	}
}

.readalso__wrapper {
	align-items     : flex-end;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 53px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 83px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.readalso__title {
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 0;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 72px;
	}
}

.readalso__box {
	background-color : $black;
	color            : $white;
	max-width        : 612px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.readalso__slider {
	.b-btn--outline {
		width : 100%;
	}
}

.swiper--readalso {

	@media screen and (min-width : $break-table) {
		margin : 0 -12.5px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -20px;
	}

	.swiper-slide {
		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 33.33333%;
		}
	}
}
