.admission {
	padding : 64px 0;

	@media screen and (min-width : $break-table) {
		padding : 96px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 120px;
		padding-top    : 169px;
	}

}

.admission__container {
	position : relative;
}

.admission-lines {
	bottom   : 13px;
	height   : 120px;
	left     : -31px;
	position : absolute;
	width    : 120px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		bottom : 21px;
		height : 240px;
		left   : -85px;
		width  : 240px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 152px;
		height : auto;
		left   : -113px;
		width  : auto;
	}
}

.admission-wave {
	height   : 16px;
	left     : -52px;
	position : absolute;
	top      : -34px;
	width    : 77px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		height : 25px;
		left   : -99px;
		top    : -58px;
		width  : 118px;
	}

	@media screen and (min-width : $break-desktop) {
		left : -228px;
		top  : -77px;
	}
}

.admission-dots {
	bottom   : -20px;
	height   : 120px;
	left     : -35px;
	position : absolute;
	width    : 120px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		display : none;
	}

	@media screen and (min-width : $break-desktop) {
		bottom  : 162px;
		display : block;
		height  : auto;
		left    : unset;
		right   : -498px;
		width   : auto;
	}
}

.admission-rhombus {
	height   : 24px;
	position : absolute;
	right    : 73px;
	top      : 497px;
	width    : 24px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		height : 40px;
		width  : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : -27px;
		left   : 460px;
		top    : unset;
	}
}

.admission-circle {
	bottom   : 159px;
	height   : 33px;
	position : absolute;
	right    : -32px;
	width    : 33px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		bottom : unset;
		height : 52px;
		right  : 102px;
		top    : -129px;
		width  : 52px;
	}

	@media screen and (min-width : $break-desktop) {
		right : -165px;
		top   : 0;
	}
}

.admission__box {
	background-color : $primary;
	margin-bottom    : 53px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 100px;
		max-width     : 440px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 100px;
		max-width     : none;
	}
}

.admission__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 0;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 72px;
	}

}

.admission__wrapper {
	position : relative;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : flex;
	}
}

.admission__image {
	position : relative;

	img {
		object-fit : cover;
		width      : 100%;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		width : 50%;
	}
}

.admission__steps {

	.b-btn--black {
		font-size   : 14px;
		line-height : 20px;
		white-space : normal;
		width       : 100%;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 32px;
		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-left : 59px;
		width        : 50%;
	}

}

.steps {
	margin-bottom : 24px;
	padding-left  : 38px;
	position      : relative;


	@media screen and (min-width : $break-table) {
		margin-bottom : 59px;
		padding-left  : 96px;
		width         : 75%;
	}

	@media screen and (min-width : $break-desktop) {
		padding-left : 88px;
		width        : 100%;
	}

	&__wrap {
		position : relative;

		&:before {
			background-color : $primary;
			border-radius    : 100%;
			content          : "";
			height           : 16px;
			left             : -45px;
			position         : absolute;
			top              : 17px;
			width            : 16px;
			z-index          : 1;

			@media screen and (min-width : $break-table) {
				left : -104px;
			}

			@media screen and (min-width : $break-desktop) {
				left : -96px;
			}
		}

		&:after {
			background-color : #D3D3D3;
			content          : "";
			height           : 1px;
			left             : -39px;
			position         : absolute;
			top              : 23px;
			width            : 39px;

			@media screen and (min-width : $break-table) {
				left  : -90px;
				top   : 24px;
				width : 90px;
			}

			@media screen and (min-width : $break-desktop) {
				left : -90px;
				top  : 24px;
			}
		}

		&--lightblue {
			&:before {
				background-color : $light-blue;
			}
		}

		&--green {
			&:before {
				background-color : $green;
			}
		}

		&--pink {
			&:before {
				background-color : $pink;
			}
		}


		&--blue {
			&:before {
				background-color : $blue;
			}
		}

	}


	&__item {
		position : relative;

		&:not(:last-child) {
			padding-bottom : 48px;

			&:before {
				background-color : #D3D3D3;
				content          : '';
				height           : 100%;
				left             : -37px;
				position         : absolute;
				top              : 25px;
				width            : 1px;

				@media screen and (min-width : $break-table) {
					left : -97px;
				}

				@media screen and (min-width : $break-desktop) {
					left : -88px;
				}
			}
		}

		&:not(:first-child) {

		}


	}

	&__box {
		display       : inline-block;
		font-size     : 16px;
		font-weight   : 400;
		line-height   : 24px;
		margin-bottom : 19px;
	}

	&__name {
		color       : $black;
		font-size   : 14px;
		font-weight : 800;
		line-height : 20px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

