// Arrow
.swiper-button-prev,
.swiper-button-next {
	cursor                      : pointer;
	display                     : inline-block;
	flex                        : 0 0 46px;
	height                      : 36px;
	outline                     : 0;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	transition                  : opacity .3s ease;
	user-select                 : none;
	vertical-align              : top;
	width                       : 36px;

	svg {
		fill       : transparent;
		height     : 36px;
		transition : fill .3s ease;
		width      : 36px;

		path {
			fill : $black;
		}
	}

	&:hover {
		svg {
			fill       : $black;
			transition : fill .3s ease;

			path {
				fill : $white;
			}
		}
	}

	@media screen and (min-width : $break-table) {
		height : 64px;
		width  : 64px;

		svg {
			height : 64px;
			width  : 64px;
		}
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:after {
		content  : '';
		left     : 0;
		position : absolute;
		top      : 0;
	}

	@media screen and (min-width : 1076px) {

	}

	&.swiper-button-disabled {
		cursor         : auto;
		opacity        : 0.3;
		pointer-events : none;
	}
}

.swiper-button-prev {
	margin-right : 6px;

	@media screen and (min-width : $break-table) {
		margin-right : 4px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-right : 8px;
	}
}

.swiper-button-next {
	margin-left : 6px;

	@media screen and (min-width : $break-table) {
		margin-left : 4px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-right : 8px;
	}
}

.slider__pagination {
	display         : flex;
	flex-flow       : row nowrap;
	justify-content : space-between;
}
