.why {
	background-color : $primary;
	padding-bottom   : 189px;
	padding-top      : 64px;
	position         : relative;

	@media screen and (min-width : $break-table) {
		padding : 96px 0;
	}

	@media screen and (min-width : 1800px) {
		padding-bottom : 135px;
		padding-top    : 120px;
	}

	.dots {
		bottom   : -189px;
		height   : 58px;
		position : absolute;
		right    : 0;
		width    : 115px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			bottom    : 0px;
			height    : 260px;
			right     : -88px;
			transform : rotate(270deg);
			width     : 251px;
		}

		@media screen and (min-width : 1800px) {
			bottom    : -3px;
			height    : auto;
			right     : 0;
			transform : rotate(360deg);
			width     : auto;
		}

	}
}

.why__container {
	max-width : 1296px;
	position  : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1440px) {
		padding-left : 100px;
	}


	.lines {
		left     : -500px;
		position : absolute;
		top      : 68px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			height : 240px;
			left   : -84px;
			top    : -60px;
			width  : 240px;
		}

		@media screen and (min-width : 1800px) {
			height : auto;
			left   : -500px;
			top    : 68px;
			width  : auto;
		}
	}

	.wave {
		height   : 16px;
		position : absolute;
		right    : -29px;
		top      : 20px;
		width    : 73px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			height : auto;
			right  : 32px;
			top    : -27px;
			width  : auto;
		}

		@media screen and (min-width : 1800px) {
			right : 0;
			top   : -53px;
		}
	}

	.rectangle {
		height   : 20px;
		left     : 18px;
		position : absolute;
		top      : -38px;
		width    : 20px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			display : none;
		}

		@media screen and (min-width : 1800px) {
			display : block;
			left    : -122px;
			top     : -57px;
		}
	}

	.rhombus {
		position : absolute;
		right    : -174px;
		top      : 200px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			right : 72px;
			top   : 288px;
		}

		@media screen and (min-width : 1800px) {
			right : -174px;
			top   : 200px;
		}
	}
}

.why__wrapper {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	grid-row-gap   : 24px;


	@media screen and (min-width : $break-table) {
		grid-row-gap : 48px;
	}

	@media screen and (min-width : 1800px) {
		flex-direction  : row;
		grid-column-gap : 102px;
		grid-row-gap    : 0;
	}
}

.why__left {
	grid-column : 4 span;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1800px) {
		grid-column : 6 span;
		position    : relative;
		width       : 85%;

	}
}

.why__wrap {
	color    : $black;
	position : relative;
	z-index  : 2;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1800px) {
		max-width : none;
	}
}

.why__main {

	grid-column : 4 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1800px) {
		grid-column : 6 span;
	}
}

.why__img {
	bottom   : -248px;
	left     : 3px;
	position : absolute;
	width    : 250px;
	z-index  : 3;

	@media screen and (min-width : $break-table) {
		display : none;
	}

	@media screen and (min-width : 1800px) {
		display : block;
		left    : 40px;
		top     : 100%;
		width   : 100%;
	}
}

.why__img--md {
	display : none;

	@media screen and (min-width : $break-table) {
		bottom   : -176px;
		display  : block;
		position : absolute;
		right    : 18px;
		width    : 352px;
		z-index  : 2;
	}

	@media screen and (min-width : 1800px) {
		display : none;
	}
}

.why__text {
	font-size   : 14px;
	font-weight : 800;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;
		max-width   : 411px;
	}

	@media screen and (min-width : 1800px) {
		max-width : none;
	}

	&--top {
		display       : block;
		font-size     : 16px;
		margin-bottom : 12px;

		@media screen and (min-width : $break-table) {
			font-size     : 24px;
			margin-bottom : 32px;
		}

		@media screen and (min-width : 1800px) {
			margin-bottom : 48px;
		}
	}
}

.why-earnings {
	align-items           : flex-start;
	display               : grid;
	grid-template-columns : repeat(6, 1fr);
	margin-bottom         : 26px;

	@media screen and (min-width : $break-table) {
		grid-column-gap : 48px;
		margin-bottom   : 50px;
	}

	@media screen and (min-width : 1800px) {
		grid-column-gap : 40px;
	}
}

.why-earnings__item {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	grid-column    : 3 span;

}

.why-earnings__box {
	color         : $black;
	font-size     : 16px;
	font-weight   : 800;
	line-height   : 24px;
	margin-bottom : 26px;
	padding       : 16px 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : 1800px) {
		margin-bottom : 34px;
	}

	&--green {
		background-color : $green;
	}

	&--blue {
		background-color : $blue;
		color            : $white;
	}
}

.why-earnings__text {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;
	}

	@media screen and (min-width : 1800px) {

	}
}

@keyframes robotMobile {
	0% {
		transform : translatey(0px);
	}
	50% {
		transform : translatey(-20px);
	}
	100% {
		transform : translatey(0px);
	}
}

@keyframes robotTable {
	0% {
		transform : translatey(0px);
	}
	50% {
		transform : translatey(-20px);
	}
	100% {
		transform : translatey(0px);
	}
}

@keyframes robotDesc {
	0% {
		transform : translatey(0px);
	}
	50% {
		transform : translatey(-30px);
	}
	100% {
		transform : translatey(0px);
	}
}

@keyframes robotShadow {
	0% {
		opacity : 1;
	}
	50% {
		opacity : 0.7;
	}
	100% {
		opacity : 1;
	}
}

.why__img {
	animation : robotMobile 5s ease-in-out infinite;
	z-index   : 10;
	@media screen and (min-width : $break-table) {
		animation : robotTable 5s ease-in-out infinite;
	}
	@media screen and (min-width : $break-desktop) {
		animation : robotDesc 5s ease-in-out infinite;
	}
}
