// -----------------------------------------------
//  Form config
// -----------------------------------------------
// Error
$form-label-color  : #A7A8B1;
$form-label-size   : 12px;
$form-label-weight : 400;
$form-label-margin : 0 0 10px;
$form-error-color  : $red;
$form-error-size   : 13px;
$form-error-weight : 500;

// Hint
$form-hint-color   : #444;
$form-hint-size    : 12px;
$form-hint-weight  : 400;
$form-hint-style   : italic;
$form-hint-margin  : 5px 0 0;

// Grid
$form-grid         : 7px;

// -----------------------------------------------
//  Form style
// -----------------------------------------------
.form {
	.b-btn--black {
		width : 100%;
	}
}

.form__row {
	display        : flex;
	flex-direction : column;
	position       : relative;
	width          : 100%;


	input[type="text"],
	input[type="email"],
	input[type="search"],
	input[type="number"],
	input[type="tel"],
	input[type="password"],
	select,
	textarea {
		vertical-align : top;
		width          : 100%;
	}

	//> label:not(.checkbox-style) {
	//	align-items     : center;
	//	color           : $form-label-color;
	//	cursor          : pointer;
	//	display         : flex;
	//	flex-flow       : row nowrap;
	//	font-size       : $form-label-size;
	//	font-weight     : $form-label-weight;
	//	justify-content : space-between;
	//	line-height     : 1.2;
	//	margin          : $form-label-margin;
	//
	//	> span {
	//		order : 0;
	//	}
	//
	//	.error-message {
	//		margin-left : 10px;
	//		margin-top  : 0;
	//
	//		+ span {
	//			color : $red;
	//		}
	//	}
	//}

	&:not(:last-child) {
		margin-bottom : 24px;
	}

	&--error {
		.form__error {
			height  : auto;
			opacity : 1;
		}

		.form__input {
			border-bottom : 2px solid $red;
		}
	}
}

.form__input {
	border-bottom : 2px solid $black;
	position      : relative;
	z-index       : 1;
}

.form__body {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 36px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}

.form__error {
	color       : $red;
	font-size   : 14px;
	font-weight : 400;
	height      : 0;
	line-height : 20px;
	opacity     : 0;
}

.form__label {
	span {
		color       : $black;
		display     : block;
		font-size   : 14px;
		font-weight : 400;
		line-height : 20px;
	}
}

.form__group {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		justify-content : space-between;
		margin-bottom   : 40px;
	}

	.form__row {
		&:not(:last-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 0;
			}
		}

		&:not(:first-child) {
			@media screen and (min-width : $break-desktop) {
				margin-left : 40px;
			}
		}
	}
}

.form__hint {
	color       : $form-hint-color;
	font-size   : $form-hint-size;
	font-style  : $form-hint-style;
	font-weight : $form-hint-weight;
	margin      : $form-hint-margin;
	position    : relative;
	z-index     : 2;
}

.form__error {
	color       : $form-error-color;
	font-size   : $form-error-size;
	font-weight : $form-error-weight;
	margin-top  : 3px;
	position    : relative;
	z-index     : 3;
}

.form-password {
	position : relative;

	input[type="text"],
	input[type="password"] {
		padding-right : 50px;
		position      : relative;
		z-index       : 1;
	}
}

.form-password__button {
	background                  : transparent;
	border                      : 0;
	bottom                      : 0;
	-moz-box-sizing             : border-box;
	-webkit-box-sizing          : border-box;
	box-sizing                  : border-box;
	cursor                      : pointer;
	opacity                     : .3;
	outline                     : 0;
	padding                     : 0;
	position                    : absolute;
	right                       : 0;
	-webkit-tap-highlight-color : transparent;
	top                         : 0;
	transition                  : opacity .3s ease;
	vertical-align              : middle;
	width                       : 42px;
	z-index                     : 10;

	i {
		position       : relative;
		vertical-align : middle;
		z-index        : 1;
	}

	&:after {
		background : #000;
		border-top : 1px solid #FFF;
		content    : '';
		display    : block;
		height     : 1px;
		left       : 9px;
		position   : absolute;
		top        : 24px;
		transform  : rotate(-30deg);
		transition : opacity .3s ease;
		width      : 24px;
		z-index    : 10;
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		&:hover {
			opacity : 1;
		}
	}

	&.active {
		opacity : 1;

		&:after {
			opacity : 0;
		}
	}
}
