.advantages {
	padding : 64px 0;

	@media screen and (min-width : $break-table) {
		padding : 96px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 120px;
		padding-top    : 70px;
	}
}

.advantages-rectangle {
	display  : none;
	position : absolute;
	right    : 0;
	top      : 0;
	width    : 28px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		display : block;
		right   : 16px;
		top     : 54px;
	}

	@media screen and (min-width : $break-desktop) {
		right : -99px;
		top   : 70px;
	}
}

.advantages-lines {
	position : absolute;
	right    : -82px;
	top      : 485px;
	width    : 160px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		width : 240px;

	}

	@media screen and (min-width : $break-desktop) {
		left  : -82px;
		top   : 120px;
		width : auto;
	}
}

.advantages-rhombus {
	bottom   : -69px;
	display  : none;
	left     : 10px;
	position : absolute;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		bottom  : -69px;
		display : block;
		left    : 10px;
	}

	@media screen and (min-width : $break-desktop) {
		left : -259px;
		top  : 617px;
	}
}

.advantages-circle {
	bottom   : 540px;
	position : absolute;
	right    : -210px;
	z-index  : -1;
}

.advantages-dots {
	position : absolute;
	z-index  : -1;

	&--bottom {
		bottom : -300px;
		right  : -322px;
	}

	&--top {
		right : -490px;
		top   : 500px;
	}

	&--middle {
		display : none;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			bottom  : 400px;
			display : block;
			left    : -600px;
		}
	}
}

.advantages__container {
	max-width : 1218px;
	position  : relative;
}

.advantages__main {
	display               : grid;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 64px;
	position              : relative;

	@media screen and (min-width : $break-table) {
		align-items           : self-start;
		grid-column-gap       : 38px;
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 100px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 70px;
	}
}

.advantages__image {
	grid-column : 4 span;

	img {
		object-fit : cover;
		width      : 100%;
	}

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		grid-row    : 1/3;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.box-img {
	background-color : #00E493;
	grid-column      : 4 span;
	mask-image       : url("../images/icon/light-blue.svg");
	mask-position    : 50% 50%;
	mask-size        : 100% 100%;
	max-width        : none;
	object-fit       : cover;
	padding          : 5px;
	position         : relative;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;

	}

	@media screen and (min-width : $break-desktop) {

	}

	&:after {
		border        : 5px solid #00F;
		border-radius : 11.68px;
		content       : "";
		display       : block;
		height        : 100%;
		position      : absolute;
		right         : -8px;
		top           : -9px;
		width         : 100%;
		z-index       : -1;

		@media screen and (min-width : $break-table) {
			height : 95%;
			right  : -16px;
			top    : -17px;
			width  : 100%;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.box-img__image {


	img {
		height        : 100%;
		mask-image    : url("../images/icon/light-blue.svg");
		mask-position : 50% 50%;
		mask-size     : 100% 100%;
		max-width     : none;
		object-fit    : cover;
		width         : 100%;
	}
}


.advantages__info {
	align-items : center;
	display     : flex;
	grid-column : 4 span;


	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
		margin-top  : 36px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		flex-direction  : column;
		grid-column     : 6 span;
		justify-content : flex-end;
		margin-top      : 0;
		padding-top     : 50px;
	}
}

.advantages__robot {

	@media screen and (min-width : $break-table) {
		flex-shrink : 0;

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 50px;
	}

}

.advantages__title {
	color         : $black;
	font-size     : 16px;
	font-weight   : 800;
	line-height   : 24px;
	margin-bottom : 0;
	margin-left   : 12px;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 40px;
		margin-left : 45px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 40px;
		line-height : 48px;
		margin-left : 0;
	}
}

.box-item--advantages {
	font-size   : 16px;
	font-weight : 800;
	grid-column : 4 span;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		grid-column : 4 span;
		line-height : 28px;
		max-width   : 205px;

		&:nth-child(2) {
			grid-column : 7/11;
		}

		&:nth-child(3) {
			grid-column : 9/13;
		}
	}

	@media screen and (min-width : $break-desktop) {


		&:nth-child(2) {
			position : absolute;
			right    : -76px;
			top      : 0;
		}

		&:nth-child(3) {
			position : absolute;
			right    : 13px;
			top      : 120px;
		}
	}
}

.advantages__wrapper {
	counter-reset         : count;
	display               : grid;
	grid-gap              : 12px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 40px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.advantages__item {
	border-radius : 12px;
	grid-column   : 6 span;
	overflow      : hidden;
	padding       : 96px 16px 35px 16px;
	position      : relative;

	@media screen and (min-width : $break-table) {
		padding : 187px 48px 70px 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		color             : $black;
		content           : '0' counter(count);
		counter-increment : count;
		font-size         : 14px;
		font-weight       : 400;
		left              : 16px;
		line-height       : 16px;
		position          : absolute;
		top               : 16px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			left        : 48px;
			line-height : 32px;
			top         : 48px;
		}
	}

	&--green {
		background-color : $green;

		.dots {
			position : absolute;
			right    : -4px;
			top      : 56px;
			width    : 112px;
			z-index  : 0;

			@media screen and (min-width : $break-table) {
				right : 19px;
				top   : 95px;
				width : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.line {
			bottom   : 12px;
			position : absolute;
			right    : 48px;
			width    : 68px;
			z-index  : 0;

			@media screen and (min-width : $break-table) {
				bottom : 30px;
				right  : 48px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.rhombus {
			bottom   : -10px;
			left     : 61px;
			position : absolute;
			width    : 22px;
			z-index  : 0;

			@media screen and (min-width : $break-table) {
				bottom : -20px;
				left   : 152px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.hat {
			position : absolute;
			right    : 19px;
			top      : 17px;
			width    : 112px;
			z-index  : 2;

			@media screen and (min-width : $break-table) {
				right : 42px;
				top   : 40px;
				width : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	&--pink {
		background-color : $pink;

		.dots {
			bottom   : 79px;
			position : absolute;
			right    : 0px;
			width    : 120px;
			z-index  : 0;

			@media screen and (min-width : $break-table) {
				bottom : 17px;
				right  : 94px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.rhombus {
			position : absolute;
			right    : -11px;
			top      : 22px;
			width    : 22px;
			z-index  : 0;

			@media screen and (min-width : $break-table) {
				right : -20px;
				top   : 76px;
				width : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.wave {
			left      : 34px;
			position  : absolute;
			top       : 170px;
			transform : rotate(90deg);
		}

		.gamepad {
			position : absolute;
			right    : 0;
			top      : 10px;
			width    : 141px;
			z-index  : 2;

			@media screen and (min-width : $break-table) {
				right : 78px;
				top   : 0;
				width : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	&--lightblue {
		background-color : $light-blue;

		.dots {
			bottom   : 51px;
			left     : -30px;
			position : absolute;
			width    : 103px;
			z-index  : 0;

			@media screen and (min-width : $break-table) {
				bottom : -111px;
				left   : -99px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.wave {
			bottom    : 19px;
			position  : absolute;
			right     : -25px;
			transform : rotate(360deg);
			width     : 76px;

			@media screen and (min-width : $break-table) {
				bottom : 25px;
				right  : -25px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.rectangle {
			left     : 97px;
			position : absolute;
			top      : 82px;
			width    : 25px;
			z-index  : 0;

			@media screen and (min-width : $break-table) {
				left  : 197px;
				top   : 167px;
				width : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.schedule {
			position : absolute;
			right    : 31px;
			top      : 40px;
			width    : 75px;
			z-index  : 2;

			@media screen and (min-width : $break-table) {
				right : 83px;
				top   : 40px;
				width : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	&--blue {
		background-color : $blue;

		.lines {
			position : absolute;
			right    : -10px;
			top      : -30px;
			width    : 120px;
			z-index  : 0;

			@media screen and (min-width : $break-table) {
				right : -34px;
				top   : -57px;
				width : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.wave {
			left      : 76px;
			position  : absolute;
			top       : 70px;
			transform : rotate(360deg);
			width     : 52px;

			@media screen and (min-width : $break-table) {
				left  : 112px;
				top   : 105px;
				width : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.rectangle {
			bottom   : 22px;
			left     : 168px;
			position : absolute;
			width    : 25px;
			z-index  : 0;

			@media screen and (min-width : $break-table) {
				bottom : 48px;
				left   : 248px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.board {
			position : absolute;
			right    : 33px;
			top      : 27px;
			width    : 60px;
			z-index  : 2;

			@media screen and (min-width : $break-table) {
				right : 92px;
				top   : 48px;
				width : auto;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}


		&:before {
			color : $white;
		}
	}
}

.advantages__wrap {
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : end;
	min-height      : 171px;
	padding         : 16px 16px;
	position        : relative;
	z-index         : 1;

	@media screen and (min-width : $break-table) {
		min-height : 322px;
		padding    : 40px 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.advantages__name {
	color         : $black;
	font-size     : 14px;
	font-weight   : 800;
	line-height   : 20px;
	margin-bottom : 18px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.advantages__text {
	color       : $black;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
