.programs {
	background-color : $primary;
	padding          : 64px 0;

	@media screen and (min-width : $break-table) {
		padding : 100px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.programs__container {
	position : relative;
}

.programs-dots {
	position : absolute;
	right    : -484px;
	top      : 0;
	z-index  : 1;

	&--mid {
		left : -463px;
		top  : 654px;
	}
}

.programs-circle {

	left     : -187px;
	position : absolute;
	top      : 120px;
	z-index  : 1;
}

.programs-rectangle {
	display  : none;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom  : 177px;
		display : block;
		left    : 55px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 449px;
		left   : -202px;
	}
}

.programs-wave {
	position : absolute;
	right    : -241px;
	top      : 704px;
	z-index  : 1;
}


.programs-lines {
	height   : 144px;
	position : absolute;
	right    : -54px;
	top      : -24px;
	width    : 144px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		height : 204px;
		right  : -69px;
		top    : -30px;
		width  : 204px;
	}

	@media screen and (min-width : $break-desktop) {
		height : auto;
		right  : -137px;
		top    : -80px;
		width  : auto;
	}
}

.programs-rhombus {
	display  : none;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom  : 40px;
		display : block;
		right   : 31px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 531px;
		right  : -234px;
	}

}


.programs-banner {
	align-items     : center;
	display         : flex;
	height          : 88px;
	justify-content : space-between;
	margin-bottom   : 75px;
	padding         : 16px 16px;
	position        : relative;
	z-index         : 2;

	@media screen and (min-width : $break-table) {
		height  : 240px;
		padding : 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 260px;
	}


	&__title {
		color         : $black;
		font-size     : 20px;
		font-weight   : 800;
		line-height   : 28px;
		margin-bottom : 0;

		@media screen and (min-width : $break-table) {
			font-size   : 40px;
			line-height : 48px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 64px;
			line-height : 72px;
		}
	}

	&__desktop {
		width : 61px;

		img {
			max-width : none;
			width     : 92px;
		}

		@media screen and (min-width : $break-table) {
			flex-shrink : 0;
			margin-top  : -31px;
			width       : 285px;

			img {
				width : 285px;
			}
		}

		@media screen and (min-width : $break-desktop) {
			margin-top : -124px;
			width      : 404px;

			img {
				width : 464px;
			}
		}
	}
}

.programs-list {
	display               : grid;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 48px;

	@media screen and (min-width : $break-table) {
		grid-row-gap  : 40px;
		margin-bottom : 96px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 40px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 80px;
	}

	&__item {
		align-items : flex-start;
		display     : flex;
		grid-column : 4 span;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}

		span {
			color       : $black;
			font-size   : 14px;
			font-weight : 400;
			line-height : 20px;
			margin-left : 24px;

			@media screen and (min-width : $break-table) {
				font-size   : 20px;
				line-height : 32px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		img {
			flex-shrink : 0;
			height      : 32px;
			width       : 32px;
			@media screen and (min-width : $break-table) {
				height : 40px;
				width  : 40px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

}

.programs__wrapper {
	margin-bottom : 56px;
	position      : relative;
	z-index       : 2;

	.b-btn--outline {
		width : 100%;
	}

	@media screen and (min-width : $break-table) {
		margin-bottom : 96px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 120px;
	}

}

.programs__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 24px;
	max-width     : 268px;

	@media screen and (min-width : $break-table) {
		font-size     : 32px;
		line-height   : 42px;
		margin-bottom : 30px;
		max-width     : 410px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 40px;
		max-width     : none;
	}
}

.programs__wrap {
	display               : grid;
	grid-row-gap          : 12px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 32px;

	@media screen and (min-width : $break-table) {
		grid-row-gap  : 24px;
		margin-bottom : 36px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 40px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 40px;
	}
}

.programs__item {
	border-radius : 12px;
	grid-column   : 4 span;
	overflow      : hidden;
	padding       : 32px 32px;
	position      : relative;

	@media screen and (min-width : $break-table) {
		padding : 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.box-item--default {
		margin-bottom : 56px;
		padding       : 24px 24px;
		position      : relative;
		z-index       : 2;

		@media screen and (min-width : $break-table) {
			padding : 40px 40px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	ul {

		@media screen and (min-width : $break-table) {
			display : flex;
			gap     : 20px;
		}

		@media screen and (min-width : $break-desktop) {
			display : block;
			gap     : 0;
		}

		li {


			&:not(:last-child) {
				margin-bottom : 24px;
			}

			@media screen and (min-width : $break-table) {
				width : 33.33333%;
				&:not(:last-child) {
					margin-bottom : 0;
				}
			}

			@media screen and (min-width : $break-desktop) {
				width : 100%;

				&:not(:last-child) {
					margin-bottom : 16px;
				}
			}

			p {
				color       : $black;
				font-size   : 12px;
				font-weight : 400;
				line-height : 16px;

				&:not(:last-child) {
					margin-bottom : 8px;
				}

				@media screen and (min-width : $break-table) {
					font-size   : 14px;
					line-height : 20px;
					max-width   : 132px;
				}

				@media screen and (min-width : $break-desktop) {
					max-width : none;
				}
			}

			span {
				color       : $black;
				display     : block;
				font-size   : 16px;
				font-weight : 800;
				line-height : 24px;

				@media screen and (min-width : $break-table) {
					font-size   : 24px;
					line-height : 32px;

					&:not(:last-child) {
						margin-bottom : 5px;
					}
				}

				@media screen and (min-width : $break-desktop) {

				}
			}
		}
	}

	.b-btn--black {
		font-size : 14px;
		position  : relative;
		width     : 100%;
		z-index   : 2;

		@media screen and (min-width : $break-table) {
			font-size : 20px;
			width     : auto;
		}
	}
}

.programs__name {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 48px;
		line-height   : 58px;
		margin-bottom : 48px;
	}
}

.programs__item--lightblue {
	background-color : $light-blue;

	.programs__dots {
		height   : 156px;
		position : absolute;
		right    : -43px;
		top      : -61px;
		width    : 156px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			height : 240px;
			right  : -74px;
			top    : 79px;
			width  : 240px;
		}

		@media screen and (min-width : $break-desktop) {
			height : auto;
			right  : -152px;
			top    : -94px;
			width  : auto;
		}
	}

	.programs__rhombus {
		bottom   : 150px;
		left     : -20px;
		position : absolute;
		z-index  : 1;


		@media screen and (min-width : $break-table) {
			bottom : -15px;
			left   : 468px;
		}

		@media screen and (min-width : $break-desktop) {
			bottom : -21px;
			left   : 147px;
		}

	}
}

.programs__item--green {
	background-color : $green;

	.programs__dots {
		height   : 156px;
		left     : -63px;
		position : absolute;
		top      : -63px;
		width    : 156px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			bottom : 72px;
			height : 240px;
			left   : unset;
			right  : -11px;
			top    : unset;
			width  : 240px;
		}

		@media screen and (min-width : $break-desktop) {
			bottom : -47px;
			height : auto;
			right  : -134px;
			top    : unset;
			width  : auto;
		}
	}

	.programs__rhombus {
		bottom   : 150px;
		left     : -20px;
		position : absolute;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			left : -20px;
			top  : 170px;
		}

		@media screen and (min-width : $break-desktop) {
			bottom : unset;
			left   : -20px;
			top    : 164px;
		}

	}

	.programs__wave {
		position  : absolute;
		right     : -39px;
		top       : -37px;
		transform : rotate(90deg);
		z-index   : 1;

		@media screen and (min-width : $break-table) {
			right : 62px;
			top   : -18px;

		}

		@media screen and (min-width : $break-desktop) {
			right : 16px;
			top   : 59px;
		}
	}
}

.programs__item--blue {
	background-color : $blue;

	.b-btn--black {
		&:hover {
			background-color : $white;
			color            : $black;
		}
	}

	.programs__lines {
		bottom   : 0;
		position : absolute;
		right    : -46px;
		width    : 156px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			bottom : -33px;
			height : 240px;
			left   : -50px;
			right  : unset;
			width  : 240px;
		}

		@media screen and (min-width : $break-desktop) {
			bottom : 0;
			height : auto;
			left   : 0;
			width  : auto;
		}

	}

	.programs__rhombus {
		display  : none;
		position : absolute;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			display : block;
			right   : 25px;
			top     : 100px;
		}

		@media screen and (min-width : $break-desktop) {
			right : 25px;
			top   : 218px;
		}
	}

	.programs__line {
		position  : absolute;
		right     : 0;
		top       : 80px;
		transform : rotate(270deg);
		z-index   : 1;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.programs__wave {
		left      : -65px;
		position  : absolute;
		top       : -11px;
		transform : rotate(219deg);
		z-index   : 1;

		@media screen and (min-width : $break-table) {
			left      : 0;
			top       : 80px;
			transform : unset;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.programs__certificate {
	align-items           : center;
	display               : grid;
	grid-row-gap          : 50px;
	grid-template-columns : repeat(4, 1fr);
	position              : relative;
	z-index               : 2;

	@media screen and (min-width : $break-table) {
		grid-row-gap : 50px;

	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 50px;
		grid-template-columns : repeat(12, 1fr);
	}


	.box-item--default {
		background-color : $black;
		grid-column      : 4 span;
		padding          : 16px 16px;

		@media screen and (min-width : $break-table) {
			grid-column : 6 span;
			padding     : 48px 48px;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 1/8;
			margin-top  : -60px;
			max-width   : 651px;
		}

		span {
			color       : $white;
			font-size   : 20px;
			font-weight : 800;
			line-height : 28px;

			@media screen and (min-width : $break-table) {
				font-size   : 32px;
				line-height : 42px;
			}

			@media screen and (min-width : $break-desktop) {
				font-size   : 48px;
				line-height : 56px;
			}
		}
	}
}

.programs__image {
	grid-column : 4 span;
	margin      : 0 auto;
	max-width   : 395px;
	position    : relative;
	text-align  : center;
	z-index     : 2;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 8/13;
		margin      : 0;
		max-width   : none;
		text-align  : left;
	}

	p {

		padding-top : 17px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			padding-left : 45px;
		}
	}
}
