.blog {
	background-image  : url("../images/blog/blog-bg.webp");
	background-repeat : no-repeat;
	background-size   : cover;
	padding-bottom    : 65px;
	padding-top       : 110px;

	.no-webp & {
		background-image : url("../images/blog/blog-bg.jpg");
	}

	@media screen and (min-width : $break-table) {
		padding-bottom : 114px;
		padding-top    : 167px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 96px;
	}
}

.blog__box {
	max-width : 96px;

	@media screen and (min-width : $break-table) {
		max-width : 167px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 210px;
	}
}

.blog__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 0;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 56px;
	}
}

.blog-wrapper {
	padding-bottom : 64px;
	padding-top    : 64px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 96px;
		padding-top    : 80px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 120px;
		padding-top    : 80px;
	}
}

.blog-grid {
	display               : grid;
	grid-row-gap          : 48px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 48px;

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 25px;
		grid-row-gap          : 64px;
		grid-template-columns : repeat(8, 1fr);
		margin-bottom         : 64px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 40px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 80px;
	}
}

.blog-grid__item {
	grid-column : 4 span;
}

.pagination {
	align-items     : center;
	display         : flex;
	justify-content : center;
	position        : relative;
}

.pagination__item {
	height : 36px;
	width  : 36px;

	@media screen and (min-width : $break-table) {
		height : 64px;
		width  : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--first {
		left     : 0;
		position : absolute;
		top      : 0;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			margin-right : 24px;
			position     : unset;

		}

		@media screen and (min-width : $break-desktop) {

		}

	}

	&--last {
		position : absolute;
		right    : 0;
		top      : 0;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			margin-left : 24px;
			position    : unset;
		}
	}

	&--num {
		&:not(:first-child) {
			margin-left : 8px;
		}
	}

	&--tablet {
		display : none;

		@media screen and (min-width : $break-table) {
			display : block;
		}
	}
}

.pagination__link {
	align-items     : center;
	border-radius   : 8px;
	color           : $black;
	display         : flex;
	font-size       : 12px;
	font-weight     : 800;
	height          : 36px;
	justify-content : center;
	line-height     : 16px;
	width           : 36px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		height      : 64px;
		line-height : 24px;
		width       : 64px;
	}

	&.active {
		background-color : $primary;

		&:hover {
			border : none;
		}
	}

	&:hover {
		border : 2px solid #000;

	}

	&--arrow {
		svg {
			fill       : $white;
			transition : fill .3s ease;

			path {
				fill : $black;
			}
		}

		&:hover {
			border : none;

			svg {
				fill       : $black;
				transition : fill .3s ease;

				path {
					fill : $white;
				}
			}
		}
	}
}
