.contacts-hero {
	background-color : $primary;
	overflow         : hidden;
	padding-top      : 86px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 0;
		padding-top    : 132px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 120px;
		padding-top    : 220px;
	}
}

.contacts-hero__container {
	position : relative;
}

.contacts-hero__wrap {
	position : relative;
	z-index  : 2;
}

.contacts-hero__box {
	margin-bottom : 41px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 72px;
		padding       : 36px 36px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 535px;
		padding   : 48px 48px;
	}
}

.contacts-hero__item {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 0;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 56px;
	}
}

.contacts-socials {
	align-items : center;
	display     : flex;

	&__item {


		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}

		&:not(:first-child) {
			margin-left : 16px;
		}

	}

	&__link {
		display : block;
		height  : 48px;
		width   : 48px;

		svg {
			height : 48px;
			width  : 48px;

			circle {
				transition : fill .3s ease;
			}

			path {
				transition : fill .3s ease;
			}
		}

		&:hover {
			svg {
				circle {
					fill       : $black;
					transition : fill .3s ease;
				}

				path {
					fill       : $white;
					transition : fill .3s ease;
				}
			}
		}

		@media screen and (min-width : $break-table) {
			height : 64px;
			width  : 64px;

			svg {
				height : 64px;
				width  : 64px;
			}
		}

		@media screen and (min-width : $break-desktop) {
			height : 80px;
			width  : 80px;

			svg {
				height : 80px;
				width  : 80px;
			}
		}

		&--vk {
			svg {
				circle {
					fill : $blue;
				}
			}
		}

		&--inst {
			svg {
				fill : $pink;
			}
		}

		&--tg {
			svg {
				fill : $green;
			}
		}
	}
}


.contacts-hero__wrapper {
	height : 100%;
	left   : 0;

	top    : 0;
	width  : 100%;

	@media screen and (min-width : $break-desktop) {
		position : absolute;
	}
}

.contacts-hero__image {
	display  : block;
	height   : 419px;
	margin   : 0 auto;
	position : relative;
	width    : 202px;
	z-index  : 2;

	@media screen and (min-width : $break-table) {
		height     : auto;
		margin-top : -125px;
		text-align : right;
		width      : auto;
	}

	@media screen and (min-width : $break-desktop) {
		bottom   : -20px !important;
		left     : unset !important;
		position : absolute;
		right    : 141px !important;
		top      : unset !important;
	}
}

.contacts-hero__dots {
	height   : 120px;
	left     : -58px;
	position : absolute;
	top      : -18px;
	width    : 120px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		height : 240px;
		left   : -116px;
		top    : -18px;
		width  : 240px;
	}

	@media screen and (min-width : $break-desktop) {
		height : auto;
		left   : -187px !important;
		top    : -81px !important;
		width  : auto;
	}
}

.contacts-hero__lines {
	bottom   : -10px;
	height   : 120px;
	position : absolute;
	right    : -28px;
	width    : 120px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : -81px;
		height : 240px;
		right  : 5px;
		width  : 240px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : -147px !important;
		height : 244px;
		left   : unset !important;
		right  : -213px !important;
		top    : unset !important;
		width  : 244px;
	}
}

.contacts-hero__line {
	bottom   : 207px;
	height   : 20px;
	left     : -70px;
	position : absolute;
	width    : 128px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 65px;
		height : auto;
		left   : 14px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : -68px !important;
		left   : -134px !important;
		top    : unset !important;
	}
}

.contacts-hero__wave {
	height   : 16px;
	position : absolute;
	right    : -41px;
	top      : 70px;
	width    : 79px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : auto;
		right  : -41px;
		top    : 70px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {
		left  : unset !important;
		right : 537px !important;
		top   : -89px !important;
	}
}

.contacts-hero__rectangle {
	bottom   : 249px;
	height   : 20px;
	position : absolute;
	right    : 34px;
	width    : 20px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 136px;
		height : auto;
		right  : 9px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 218px !important;
		left   : unset !important;
		right  : -126px !important;
		top    : unset !important;
	}
}

.contacts-hero__rhombus {
	bottom   : 23px;
	height   : 20px;
	left     : 27px;
	position : absolute;
	width    : 20px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 1px;
		height : 31px;
		left   : 264px;
		width  : 31px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : -17px !important;
		height : 20px;
		left   : 615px !important;
		top    : unset;
		width  : 20px;
	}
}
