@font-face {
	font-family  : 'NeueMachina';
	src          : url('../fonts/NeueMachina-Regular.woff2') format('woff2'),
	url('../fonts/NeueMachina-Regular.woff') format('woff'),
	url('../fonts/NeueMachina-Regular.ttf') format('truetype');
	font-weight  : 400;
	font-style   : normal;
	font-display : swap;
}

