// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 800;
$btn-line-height           : 1.25;
$btn-border-width          : 1px;
$btn-padding               : 16px 24px;
$btn-font-size             : 20px;
$btn-border-radius         : 4px;
$btn-transition            : background .3s ease, color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 14px 30px;
$btn-font-size-big         : 16px;
// Small
$btn-padding-small         : 14px 16px;
$btn-font-size-small       : 14px;

// COLOR


// Black
$btn-black-bg              : #000;
$btn-black-color           : #FFF;
$btn-black-color-outline   : transparent;
$btn-black-bg-hover        : transparent;
$btn-black-border          : 2px solid #000;
$btn-black-color-hover     : #000;

// Icon
$btn-icon-bg               : #FFF;
$btn-icon-color            : #000;
$btn-icon-color-outline    : transparent;
$btn-icon-bg-hover         : #000;
$btn-icon-border           : 2px solid #000;
$btn-icon-color-hover      : #FFF;

// Link
$btn-link-bg               : transparent;
$btn-link-color            : #18A3FA;
$btn-link-bg-hover         : #F7F7F7;
$btn-link-color-hover      : darken(#18A3FA, 10%);


// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	align-items        : center;
	border             : $btn-border-width solid transparent;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	display            : inline-flex;
	flex-flow          : row nowrap;
	font-size          : $btn-font-size;
	font-weight        : $btn-font-weight;
	justify-content    : center;
	line-height        : $btn-line-height;
	padding            : $btn-padding;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	transition         : $btn-transition;
	user-select        : none;
	vertical-align     : middle;
	white-space        : nowrap;

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	svg {
		fill : currentColor;

		path {
			fill : currentColor;
		}
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR
	// Black
	&--black {
		background-color : $btn-black-bg;
		border           : 2px solid transparent;
		color            : $btn-black-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-black-bg-hover;
			border           : $btn-black-border;
			color            : $btn-black-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-black-bg;
				color            : $btn-black-color;
			}
		}
	}

	// Icon
	&--icon {
		background-color : $btn-icon-bg;
		border           : 2px solid $btn-icon-color;
		color            : $btn-icon-color;
		font-size        : 14px;
		line-height      : 20px;
		padding          : 12px 16px;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-icon-bg-hover;
			border           : $btn-icon-border;
			color            : $btn-icon-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-icon-bg;
				color            : $btn-icon-color;
			}
		}
	}

	// Link
	&--link {
		background-color : $btn-link-bg;
		color            : $btn-link-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-link-bg-hover;
			color            : $btn-link-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-link-bg;
				color            : $btn-link-color;
			}
		}
	}

	//Black
	&--outline {
		background-color : transparent;
		border           : 2px solid $btn-black-bg;
		color            : $btn-black-bg;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-black-bg;
			color            : $btn-black-color;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-black-bg;
				color            : $btn-black-color-outline;
			}
		}
	}

	// FULL-SIZE
	&--full-size {
		display : block;
		width   : 100%;

		+ .full-size {
			margin-right : $btn-spacing-y;
		}
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}

