.error {
	background-color : $primary;
	overflow         : hidden;
	padding-bottom   : 200px;
	padding-top      : 120px;
	position         : relative;

	@media screen and (min-width : $break-table) {
		padding-bottom : 300px;
		padding-top    : 250px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 247px;
		padding-top    : 228px;
	}
}

.error__wrapper {
	display         : flex;
	justify-content : center;
	position        : relative;
}

.error__wrap {
	position : relative;
	z-index  : 2;
}

.error__bg {
	color                   : $black;
	display                 : block;
	font-size               : 140px;
	font-weight             : 800;
	justify-content         : center;
	left                    : 50%;
	line-height             : 300px;
	position                : absolute;
	-webkit-text-fill-color : rgba(0, 0, 0, 0);
	-webkit-text-stroke     : 1px rgb(0, 0, 0);
	top                     : 50%;
	transform               : translate(-50%, -50%);
	user-select             : none;
	z-index                 : 1;

	@media screen and (min-width : $break-table) {
		font-size   : 360px;
		line-height : 300px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 623.068px;
		line-height : 727px;
	}
}

.error__image {
	display         : flex;
	justify-content : center;

	img {
		display   : block;
		height    : 227px;
		max-width : none;
		width     : 190px;

		@media screen and (min-width : $break-table) {
			height : 469px;
			width  : 389px;
		}

		@media screen and (min-width : $break-desktop) {
			height : 569px;
			width  : 489px;
		}
	}
}

.error__box {
	background-color : $black;
	color            : $white;
	font-size        : 16px;
	font-weight      : 800;
	left             : -55px;
	line-height      : 24px;
	max-width        : 100px;
	padding          : 16px 16px;
	position         : absolute;
	top              : -19px;
	z-index          : 1;


	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		left        : -86px;
		line-height : 32px;
		max-width   : 173px;
		padding     : 36px 36px;
		top         : -65px;

	}

	@media screen and (min-width : $break-desktop) {
		left : -50px;
		top  : -15px;
	}
}

.error__item {
	bottom   : -169px;
	position : absolute;
	right    : -46px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom    : -213px;
		max-width : 279px;
		right     : -134px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom    : 12px;
		max-width : none;
		right     : -318px;
	}


	.box-item--top {
		background-color : $white;
		color            : $black;
		font-size        : 14px;
		font-weight      : 800;
		line-height      : 20px;
		margin-bottom    : 16px;
		max-width        : 223px;
		padding          : 16px 16px;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
			max-width   : 341px;
			padding     : 32px 32px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size     : 20px;
			line-height   : 28px;
			margin-bottom : 24px;
			max-width     : 341px;
			padding       : 36px 36px;
		}
	}

	.b-btn--black {
		font-size   : 14px;
		line-height : 20px;

		@media screen and (min-width : $break-table) {
			width : 100%;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 20px;
			line-height : 32px;
			width       : auto;
		}
	}
}

.error__log {
	height    : 50px;
	left      : 50%;
	position  : absolute;
	top       : 24px;
	transform : translateX(-50%);
	width     : 128px;

	@media screen and (min-width : $break-table) {
		top   : 36px;
		width : 148px;
	}

	@media screen and (min-width : $break-desktop) {
		top : 36px;
	}
}

.error__dots {
	height   : 120px;
	left     : -54px;
	position : absolute;
	top      : -26px;
	width    : 120px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : 250px;
		left   : -123px;
		top    : 0;
		width  : 250px;
	}

	@media screen and (min-width : $break-desktop) {
		height : auto;
		left   : 104px;
		top    : -88px;
		width  : auto;
	}
}

.error__lines {
	bottom   : -25px;
	height   : 120px;
	position : absolute;
	right    : -46px;
	width    : 120px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : -52px;
		height : 175px;
		right  : -67px;
		width  : 190px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : -78px;
		height : auto;
		right  : 320px;
		width  : auto;
	}
}

.error__line {
	height   : 17px;
	position : absolute;
	right    : -76px;
	top      : 39px;
	width    : 112px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : 17px;
		right  : 31px;
		top    : 114px;
		width  : 112px;
	}

	@media screen and (min-width : $break-desktop) {
		height : auto;
		right  : 260px;
		top    : 143px;
		width  : auto;
	}

}

.error__wave {
	bottom   : 203px;
	height   : 12px;
	left     : -22px;
	position : absolute;
	width    : 56px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 278px;
		height : 20px;
		width  : 92px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 247px;
		height : auto;
		left   : 233px;
		width  : auto;
	}
}

.error__rectangle {
	height   : 16px;
	position : absolute;
	right    : 14px;
	top      : 126px;
	width    : 16px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : 31px;
		right  : 276px;
		top    : 185px;
		width  : 31px;
	}

	@media screen and (min-width : $break-desktop) {
		height : auto;
		right  : 612px;
		top    : 219px;
		width  : auto;
	}

}

.error__rhombus {
	bottom   : 66px;
	height   : 16px;
	left     : 50px;
	position : absolute;
	width    : 16px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 84px;
		height : 28px;
		left   : 258px;
		width  : 27px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 80px;
		height : auto;
		left   : 812px;
		width  : auto;
	}
}
