.footer {
	flex    : 0 0 auto;
	padding : 36px 0;

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : 1180px) {
		padding : 64px 0;
	}
}

.footer__container {
	position : relative;
}


.footer__wrapper {

	@media screen and (min-width : $break-table) {
		align-items    : end;
		border-bottom  : 1px solid rgba(0, 0, 0, 0.1);
		display        : flex;
		padding-bottom : 24px;
	}

	@media screen and (min-width : 1180px) {
		border-bottom  : none;
		display        : block;
		padding-bottom : 0;
	}
}


.footer__top {
	border-bottom  : 1px solid rgba(0, 0, 0, 0.1);
	padding-bottom : 24px;
	@media screen and (min-width : $break-table) {
		border-bottom  : 0;
		padding-bottom : 0;
	}

	@media screen and (min-width : 1180px) {
		align-items     : center;
		border-bottom   : 1px solid rgba(0, 0, 0, 0.1);
		display         : flex;
		justify-content : space-between;
		padding-bottom  : 32px;
	}
}

.footer__s {
	margin-bottom : 24px;
	margin-top    : 24px;
	position      : relative;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1180px) {
		align-items   : center;
		display       : flex;
		margin-bottom : 0;
		margin-top    : 0;


		&:before {
			background-color : rgba(0, 0, 0, 0.2);
			content          : "";
			height           : 48px;
			left             : -36px;
			position         : absolute;
			top              : 0;
			width            : 1px;
		}
	}

	span {
		color         : $black;
		display       : block;
		font-size     : 12px;
		font-weight   : 400;
		line-height   : 20px;
		margin-bottom : 16px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : 1180px) {
			margin-bottom : 0;
			margin-right  : 24px;
		}
	}

	.social--footer {
		.s-block__item {
			&:not(:first-child) {
				margin-left : 16px;

				@media screen and (min-width : $break-table) {
					margin-left : 20px;
				}
			}
		}
	}
}

.footer__email {
	display        : flex;
	flex-direction : column;
	margin-bottom  : 24px;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1180px) {
		margin-bottom : 0;

		&:before {
			background-color : rgba(0, 0, 0, 0.2);
			content          : "";
			height           : 48px;
			left             : -36px;
			position         : absolute;
			top              : 0;
			width            : 1px;
		}
	}

	span {
		color       : $black;
		font-size   : 12px;
		font-weight : 400;
		line-height : 20px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : 1180px) {
			text-align : right;
		}
	}

	a {
		color                : $black;
		font-size            : 20px;
		font-weight          : 800;
		line-height          : 32px;
		text-decoration-line : underline;
	}
}

.footer__backcall {
	position : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1180px) {
		align-items : center;
		display     : flex;

		&:before {
			background-color : rgba(0, 0, 0, 0.2);
			content          : "";
			height           : 48px;
			left             : -36px;
			position         : absolute;
			top              : 0;
			width            : 1px;
		}
	}

	.work-time {
		margin-bottom : 24px;
		margin-left   : 0;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : 1180px) {
			margin-bottom : 0;
			margin-right  : 24px;
		}

	}
}

.footer__middle {
	border-bottom : 1px solid rgba(0, 0, 0, 0.1);
	padding       : 24px 0;

	@media screen and (min-width : $break-table) {
		border-bottom : 0;
		padding       : 0;

	}

	@media screen and (min-width : 1180px) {
		border-bottom : 1px solid rgba(0, 0, 0, 0.1);
		padding       : 32px 0;
	}
}

.footer__menu {
	ul {

		@media screen and (min-width : $break-table) {
			padding-left : 110px;
		}

		@media screen and (min-width : 1180px) {
			align-items     : center;
			display         : flex;
			justify-content : space-between;
			padding-left    : 0;
		}

		li {

			&:not(:last-child) {
				margin-bottom : 24px;
			}

			@media screen and (min-width : 1180px) {
				&:not(:first-child) {
					padding-left : 10px;
				}

				&:not(:last-child) {
					margin-bottom : 0;
				}
			}

			a {
				color       : $black;
				font-size   : 16px;
				font-weight : 400;
				line-height : 24px;
				transition  : text-shadow .3s ease;

				&:hover {
					text-shadow : 0 0 1px $black;
					transition  : text-shadow .3s ease;
				}
			}
		}
	}
}

.footer__bottom {
	padding-top : 27px;
}

.footer__info {
	color         : rgba(0, 0, 0, 0.7);
	display       : block;
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 16px;
	margin-bottom : 24px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
		text-align  : start;
	}

	@media screen and (min-width : 1180px) {

	}

}

.footer__copyright {
	align-items    : center;
	display        : flex;
	flex-direction : column;


	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : space-between;
	}


	span {
		color         : rgba(0, 0, 0, 0.7);
		display       : inline-block;
		font-size     : 12px;
		font-weight   : 400;
		line-height   : 16px;
		margin-bottom : 16px;

		@media screen and (min-width : $break-table) {
			font-size     : 14px;
			line-height   : 20px;
			margin-bottom : 0;
		}
	}

	a {
		color                : $black;
		font-size            : 12px;
		font-weight          : 400;
		line-height          : 16px;
		text-decoration-line : underline;
		transition           : color .3s ease;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 20px;
		}

		&:hover {
			color : $blue;
		}
	}
}

.footer__wave {
	height   : 15px;
	position : absolute;
	right    : -16px;
	top      : 13px;
	width    : 75px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : auto;
		right  : 35px;
		top    : 13px;
		width  : auto;
	}

	@media screen and (min-width : 1180px) {
		left  : -245px;
		right : unset;
		top   : 122px;
	}
}

.footer__dots {
	bottom   : -176px;
	position : absolute;
	right    : -483px;
	z-index  : 1;
}

.footer__rhombus {
	bottom    : -45px;
	height    : 24px;
	left      : 50%;
	position  : absolute;
	transform : translateX(-50%);
	width     : 24px;
	z-index   : 1;

	@media screen and (min-width : $break-table) {
		bottom : -33px;
		height : auto;
		width  : auto;
	}

	@media screen and (min-width : 1180px) {
		bottom : -83px;
		left   : 576px;
	}
}
