.contacts {
	background-color : $primary;
	padding          : 64px 0 117px 0;

	@media screen and (min-width : $break-table) {
		padding : 95px 0 45px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}
}

.contacts__container {
	position : relative;
}

.contacts__head {
	background-color : $black;
	color            : $white;
	margin-bottom    : 33px;
	max-width        : 135px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 82px;
		max-width     : 306px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 423px;
	}
}

.contacts__title {
	color         : $white;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 0;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 72px;
	}
}

.contacts__wrapper {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
	}
}

.contacts__map {
	border-radius : 12px;
	height        : 225px;
	margin-bottom : 16px;
	overflow      : hidden;
	position      : relative;
	z-index       : 2;

	@media screen and (min-width : $break-table) {
		height        : 520px;
		margin-bottom : 36px;
	}

	@media screen and (min-width : $break-desktop) {
		height        : 564px;
		margin-bottom : 0;
		width         : 728px;
	}
}

.contacts__address {
	padding : 24px 24px;

	@media screen and (min-width : $break-table) {
		padding : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 64px;
	}
}

.address-list {
	@media screen and (min-width : $break-table) {
		display               : grid;
		grid-column-gap       : 48px;
		grid-row-gap          : 36px;
		grid-template-columns : repeat(2, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		display : block;
	}

	&__item {
		&:not(:last-child) {
			margin-bottom : 24px;
		}

		span {
			display     : block;
			font-size   : 14px;
			font-weight : 800;
			line-height : 20px;

			@media screen and (min-width : $break-table) {
				font-size   : 16px;
				line-height : 24px;
			}

			&:not(:last-child) {
				margin-bottom : 12px;

				@media screen and (min-width : $break-table) {
					margin-bottom : 8px;
				}
			}
		}
	}

	&__city {
		color : rgba(0, 0, 0, 0.4);
	}

	&__phone {
		color : $black;
	}

	&__address {
		color : $black;
	}
}

.map-order {
	height   : 564px;
	position : relative;
	z-index  : 2;
}

.contacts__lines {
	display  : none;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		display : block;
		left    : -55px;
		top     : 406px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : -28px;
		height : auto;
		left   : -138px;
		top    : unset;
		width  : auto;
	}
}

.contacts__wave {
	display  : none;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		display : block;
		right   : 128px;
		top     : 7px;
	}

	@media screen and (min-width : $break-desktop) {
		right : 420px;
	}
}

.contacts__rhombus {
	display  : none;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom  : 143px;
		display : block;
		right   : 0;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 202px;
		right  : -174px;
	}
}

.contacts__rectangle {
	display  : none;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-desktop) {
		display : block;
		left    : -132px;
		top     : 313px;
	}
}


.contacts__circle {
	display  : none;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		display : block;
		right   : -25px;
		top     : 117px;
	}

	@media screen and (min-width : $break-desktop) {
		right : -12px;
		top   : 65px;
	}
}
