.opportunities {
	padding : 66px 0;

	@media screen and (min-width : $break-table) {
		padding : 107px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}
}

.opportunities__container {
	position : relative;
}

.opportunities__top {
	margin-bottom : 48px;
	position      : relative;

	@media screen and (min-width : $break-table) {
		margin-bottom : 64px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : center;
	}
}

.opportunities__left {
	margin-bottom : 30px;
	position      : relative;
	@media screen and (min-width : $break-table) {
		margin-bottom : 50px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		max-width     : 536px;
	}
}

.opportunities__title {
	color       : $black;
	font-size   : 20px;
	font-weight : 800;
	line-height : 28px;

	@media screen and (min-width : $break-table) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.opportunities__text {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.opportunities__image {

	img {
		object-fit : cover;
		width      : 100%;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 88px;
	}
}

.opportunities__company {
	display        : flex;
	flex-direction : column-reverse;
	margin-bottom  : 48px;
	position       : relative;

	@media screen and (min-width : $break-table) {
		margin-bottom : 64px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items    : center;
		flex-direction : row;
		margin-bottom  : 95px;
		padding-top    : 34px;
	}
}

.company-list {
	display               : grid;
	grid-column-gap       : 17px;
	grid-row-gap          : 9px;
	grid-template-columns : repeat(3, 85px);
	justify-content       : center;
	position              : relative;


	@media screen and (min-width : $break-table) {
		grid-column-gap       : 41px;
		grid-row-gap          : 22px;
		grid-template-columns : repeat(3, 199px);
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 32px;
		grid-row-gap          : 16px;
		grid-template-columns : repeat(3, 149px);
		padding               : 0 25px;
	}

	&:before {
		background-image  : url("../images/home/opportunities/parentheses.svg");
		background-repeat : no-repeat;
		background-size   : contain;
		content           : "";
		height            : 15px;
		left              : 50%;
		position          : absolute;
		top               : -34px;
		transform         : translateX(-50%);
		width             : 309px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			width : 680px;
		}

		@media screen and (min-width : $break-desktop) {
			left      : 0;
			transform : unset;
			width     : 100%;
		}
	}

	&__item {
		align-items      : center;
		background-color : $gray;
		border-radius    : 100%;
		display          : flex;
		grid-column      : 1 span;
		height           : 85px;
		justify-content  : center;
		overflow         : hidden;
		padding          : 10px;
		width            : 85px;


		@media screen and (min-width : $break-table) {
			height : 199px;
			width  : 199px;
		}

		@media screen and (min-width : $break-desktop) {
			height : 140px;
			width  : 140px;
		}


		img {
			object-fit : cover;
			width      : 70%;

			@media screen and (min-width : $break-table) {
				width : auto;
			}
		}

		picture {
			display         : flex;
			justify-content : center;
		}

		&--vk {
			img {
				height    : 30px;
				max-width : none;
				width     : 30px;

				@media screen and (min-width : $break-table) {
					height    : auto;
					max-width : 100%;
					width     : auto;
				}
			}

		}
	}
}

.company--text {
	margin-bottom : 54px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 94px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		margin-left   : 144px;
	}
}

.skill {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}
}

.skill__left {
	position : relative;
}

.skill__list {
	column-gap  : 24px;
	display     : flex;
	flex-wrap   : wrap;
	padding-top : 39px;
	position    : relative;
	row-gap     : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-left  : 94px;
		padding-left : 50px;
		padding-top  : 0;
	}

	&:after {
		background-image  : url("../images/home/opportunities/parentheses-mob.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 15px;
		left              : 0;
		position          : absolute;
		top               : 0;
		width             : 100%;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			background-image : url("../images/home/opportunities/parentheses.svg");
		}

		@media screen and (min-width : $break-desktop) {
			background-image : url("../images/home/opportunities/parentheses-vert.svg");
			height           : 100%;
			top              : 50%;
			transform        : translateY(-50%);
			width            : 14px;
		}
	}
}

.skill__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}
}

.opportunities-lines {
	bottom   : -12px;
	height   : 120px;
	position : absolute;
	right    : -26px;
	width    : 120px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		height : 240px;
		width  : 240px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : -12px;
		right  : -12px;
		width  : auto;
	}
}

.opportunities-dots {
	bottom   : -30px;
	height   : 120px;
	left     : -34px;
	position : absolute;
	width    : 120px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		bottom : -73px;
		height : 240px;
		left   : unset;
		right  : -43px;
		width  : 240px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : unset;
		left   : -500px;
		right  : unset;
		top    : 300px;
		width  : auto;
	}
}

.opportunities-rhombus {
	height   : 20px;
	position : absolute;
	right    : 12px;
	top      : 7px;
	width    : 20px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		height : auto;
		left   : -207px;
		top    : 44px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.opportunities-wave {
	display : none;

	@media screen and (min-width : $break-table) {
		bottom   : -61px;
		display  : block;
		position : absolute;
		right    : -102px;
		z-index  : -1;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : unset;
		right  : -241px;
		top    : 75px;
	}
}
