.choices {
	border-radius : 4px;
	margin-bottom : 0;
	width         : 100%;

	&__item {
		border : 2px solid transparent;
	}

	&:hover {
		transition : background-color .3s ease;

		.choices__list.choices__list--single .choices__item.choices__placeholder.choices__item--selectable {
			background-color : $primary;
			border           : 2px solid $black;
			color            : $black;
			transition       : background-color .3s ease;
			width            : 100%;
		}

		.choices__item.choices__placeholder:after {
			background-image : url("../images/icon/chevron-white.svg");
		}

	}

	&.is-open {
		border-radius : 12px;

		.choices__list.choices__list--single .choices__item.choices__placeholder.choices__item--selectable {
			background-color : $black;
			border           : none;
			color            : $white;
			transition       : background-color .3s ease;
			width            : 100%;
		}

		.choices__item.choices__placeholder:after {
			background-image : url("../images/icon/chevron-white.svg");
		}
	}

	&--level {
		max-width : 114px;

		@media screen and (min-width : $break-table) {
			max-width : 168px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : 201px;
		}
	}

	&--duration {
		max-width : 157px;

		@media screen and (min-width : $break-table) {
			max-width : 229px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : 275px;
		}
	}

	&--direction {
		max-width : 152px;

		@media screen and (min-width : $break-table) {
			max-width : 223px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : 268px;
		}
	}


}

.choices.is-open.is-flipped {
	border-radius : 12px;
}

.choices[data-type*=select-one] .choices__item[placeholder] .choices__button {
	display : none;
}

.choices__inner {
	background-color : $black;
	border           : none;
	border-radius    : 4px;
	color            : #FFF;
	display          : inline-block;
	font-size        : 14px;
	font-weight      : 800;
	height           : 36px;
	line-height      : 20px;
	overflow         : hidden;
	padding          : 8px 12px;
	vertical-align   : top;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		height      : 60px;
		line-height : 28px;
		padding     : 16px 16px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		height      : 64px;
		line-height : 32px;
		padding     : 16px 24px;
	}
}

.choices__placeholder {
	opacity : 1;
}

.is-focused .choices__inner, .is-open .choices__inner {
	border : none;
}

.is-flipped.is-open .choices__inner {
	border        : 2px solid transparent;
	border-radius : 4px;

	.choices__list.choices__list--single .choices__item.choices__placeholder.choices__item--selectable {
		border : 2px solid transparent;
	}
}

.choices[data-type*=select-one] .choices__inner {
	padding-bottom : 8px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 24px;
	}

}

.choices__list--single {
	display : inline-block;
	padding : 0;
	width   : 100%;
}

.choices__list--dropdown .choices__item, .choices__list[aria-expanded] .choices__item {
	color       : #FFF;
	font-size   : 14px;
	font-weight : 800;
	line-height : 20px;
	padding     : 6px 6px;
	position    : relative;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
		padding     : 8px 8px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 32px;
		padding     : 12px 12px;
	}
}

.choices__list--dropdown .choices__item--selectable.is-highlighted, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
	background-color : $blue;
	transition       : background-color .3s ease;
}

.choices__list--dropdown .choices__list, .choices__list[aria-expanded] .choices__list {
	max-height                 : none;
	overflow                   : auto;
	-webkit-overflow-scrolling : touch;
	padding                    : 6px 6px;
	position                   : relative;
	will-change                : scroll-position;

	@media screen and (min-width : $break-table) {

		padding : 8px 8px;
	}

	@media screen and (min-width : $break-desktop) {

		padding : 12px 12px;
	}
}

.choices__list--dropdown, .choices__list[aria-expanded] {
	background-color : $black;
	border           : none;
	border-radius    : 4px;
	margin-top       : 16px;
	overflow         : hidden;
	position         : absolute;
	top              : 100%;
	visibility       : hidden;
	width            : 100%;
	will-change      : max-height;
	word-break       : break-all;
	z-index          : 11;
}


.choices__list--dropdown .choices__item--selectable::after, .choices__list[aria-expanded] .choices__item--selectable::after {
	content   : '';
	display   : none;
	font-size : 12px;
	opacity   : 0;
	position  : absolute;
	right     : 10px;
	top       : 50%;
	transform : translateY(-50%);
}

.choices__list.choices__list--single .choices__item.choices__placeholder.choices__item--selectable {
	background-color : $black;
	color            : $white;
	transition       : background-color .3s ease;
	width            : 100%;
}

.choices__item {
	position : relative
}

.choices__item.choices__placeholder:after {

	background-image  : url("../images/icon/chevron-white.svg");
	background-repeat : no-repeat;
	background-size   : 20px;
	content           : "";
	display           : inline-block;
	height            : 20px;
	margin-left       : 8px;
	pointer-events    : none;
	width             : 20px;
	z-index           : 4;

	@media screen and (min-width : $break-table) {
		background-size : 24px;
		height          : 24px;
		width           : 24px;
	}

	@media screen and (min-width : $break-desktop) {


	}
}


.choices__list.choices__list--single .choices__item.choices__item--selectable {
	align-items      : center;
	background-color : $white;
	color            : $black;
	display          : flex;
	height           : 100%;
	justify-content  : space-between;
	left             : 0;
	padding          : 8px 12px;
	position         : absolute;
	top              : 0;
	width            : 100%;
	z-index          : 6;

	@media screen and (min-width : $break-table) {

		padding : 16px 16px;
	}

	@media screen and (min-width : $break-desktop) {

		padding : 16px 24px;
	}
}


.choices[data-type*=select-one] .choices__button {
	background-image : url('../images/icon/close.svg');
	background-size  : 24px;
	border           : none;
	border-radius    : 0;
	height           : 24px;
	margin-right     : 0;
	margin-top       : 0;
	opacity          : 1;
	position         : static;
	top              : 0;
	width            : 24px;
}

.choices[data-type*=select-one]::after {
	display : none;
}


.is-flipped .choices__list--dropdown, .is-flipped .choices__list[aria-axpanded] {
	border-radius : 0;
	bottom        : unset !important;
	margin-bottom : -1px;
	margin-top    : 16px !important;
	top           : 100% !important;
	z-index       : 30 !important;
}

.choices[data-type*=select-one].is-open::after {
	border     : none;
	margin-top : 0;
}

.choices [hidden] {
	display : unset !important;
}

.choices[data-type*=select-one] .choices__input {
	background-color : #FFF;
	border-bottom    : none;
	display          : block;
	height           : 0;
	margin           : 0;
	padding          : 10px;
	width            : 100%;
}

#choices--select-level-item-choice-1 {
	display : none;
}

#choices--select-duration-item-choice-1 {
	display : none;
}

#choices--select-direction-item-choice-1 {
	display : none;
}
