.articles-box {
	background-image  : url("../images/articles/articles-bg.webp");
	background-repeat : no-repeat;
	background-size   : cover;
	padding           : 125px 0;

	.no-webp & {
		background-image : url("../images/articles/articles-bg.jpg");
	}

	@media screen and (min-width : $break-table) {
		padding : 200px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 215px 0;
	}
}

.articles {
	margin-top     : -170px;
	padding-bottom : 64px;

	@media screen and (min-width : $break-table) {
		margin-top     : -250px;
		padding-bottom : 96px;
	}
	@media screen and (min-width : $break-desktop) {
		margin-top     : -250px;
		padding-bottom : 120px;
	}
}

.articles__container {
	margin    : 0 auto;
	max-width : 794px;
}

.articles__main {
	margin-bottom : 32px;

	.b-btn--black {
		font-size     : 14px;
		line-height   : 20px;
		margin-bottom : 16px;

		@media screen and (min-width : $break-table) {
			font-size     : 20px;
			line-height   : 32px;
			margin-bottom : 36px;
		}
		@media screen and (min-width : $break-desktop) {

		}

	}
}

.articles__poster {
	height        : 224px;
	margin-bottom : 16px;
	overflow      : hidden;

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		height        : auto;
		margin-bottom : 36px;
		width         : auto;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.articles__bottom {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.articles__date {
	color       : rgba(0, 0, 0, 0.5);
	font-size   : 12px;
	font-weight : 800;
	line-height : 16px;

	@media screen and (min-width : $break-table) {;
		font-size   : 20px;
		line-height : 28px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.articles__group {
	&:not(:last-child) {
		margin-bottom : 36px;

		@media screen and (min-width : $break-table) {;
			margin-bottom : 64px;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}

.articles__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {;
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 40px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size     : 48px;
		line-height   : 56px;
		margin-bottom : 24px;
	}
}

.articles__text {
	color       : $black;
	font-size   : 14px;
	font-weight : 500;
	line-height : 20px;

	@media screen and (min-width : $break-table) {;
		font-size   : 20px;
		line-height : 32px;
	}
	@media screen and (min-width : $break-desktop) {

	}


	&:not(:last-child) {
		margin-bottom : 20px;

		@media screen and (min-width : $break-table) {;
			margin-bottom : 30px;

		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}
