.header {
	left     : 0;
	padding  : 0 12px;
	position : absolute;
	top      : 12px;
	width    : 100%;
	z-index  : 100;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1450px) {

	}

}

.header__logo {
	flex-shrink : 0;
	height      : 21px;
	width       : 87px;

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}

	@media screen and (min-width : $break-table) {
		height : 30px;
		width  : 120px;
	}

	@media screen and (min-width : 1450px) {
		height : 30px;
		width  : 130px;
	}

	&--mobile {
		display       : block;
		height        : 50px;
		margin-bottom : 20px;
		width         : 128px;

		@media screen and (min-width : $break-table) {
			height : 58px;
			width  : 148px;
		}

		@media screen and (min-width : 1450px) {
			display : none;
		}
	}
}

.header__container {
	align-items      : center;
	background-color : $white;
	border-radius    : 12px;
	display          : flex;
	justify-content  : space-between;
	margin           : 0 auto;
	max-width        : 1896px;
	padding          : 8px 16px;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		justify-content : unset;
		padding         : 18px 16px;
	}

	@media screen and (min-width : 1450px) {
		padding : 16px 24px;
	}

	.b-btn--icon {
		margin-left : 24px;
	}

	.b-btn--black {
		margin-left : 24px;
	}
}

.header__mobile {
	display         : flex;
	flex-direction  : column;
	justify-content : center;

	.s-block {
		justify-content : center;
		margin-bottom   : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 36px;
		}

		&__item {
			background-color : $white;
		}
	}

	.work-time {
		margin-bottom : 24px;
		margin-left   : 0;
		text-align    : center;

		@media screen and (min-width : $break-table) {
			margin-bottom : 36px;
		}

		@media screen and (min-width : 1450px) {

		}

		a {
			font-size   : 14px;
			line-height : 20px;

			@media screen and (min-width : $break-table) {
				font-size   : 20px;
				line-height : 28px;
			}
		}
	}

	.b-btn--black {
		margin-bottom : 12px;
		margin-left   : 0;

		@media screen and (min-width : $break-table) {
			margin-bottom : 16px;
		}

	}

	.b-btn--outline {
		margin-left : 0;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1450px) {
		display : none;
	}
}

.header__panel {


	@media screen and (min-width : $break-table) {
		align-items : center;
		display     : flex;
		justify-content : flex-end;
		width       : 100%;
	}

	@media screen and (min-width : 1450px) {
		justify-content : space-between;
	}
}

.header__wrap {
	display : none;

	@media screen and (min-width : $break-table) {
		align-items  : center;
		display      : flex;
		margin-right : 55px;
	}

	@media screen and (min-width : 1450px) {
		margin-right : 0;
	}
}

.nav {
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1450px) {
		margin-bottom : 0;
		margin-left   : 24px;
	}

	&__list {


		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : 1450px) {
			align-items : center;
			display     : flex;
		}
	}

	&__item {
		text-align : center;

		&:not(:last-child) {
			margin-bottom : 16px;
		}

		@media screen and (min-width : $break-table) {
			&:not(:last-child) {
				margin-bottom : 24px;
			}
		}

		@media screen and (min-width : 1450px) {
			text-align : left;

			&:not(:first-child) {
				margin-left : 24px;
			}

			&:not(:last-child) {
				margin-bottom : unset;
			}
		}

		&.active {
			.nav__link {
				text-shadow : 0 0 1px $black;
			}
		}
	}

	&__link {
		color       : $black;
		font-size   : 14px;
		font-weight : 800;
		line-height : 20px;
		transition  : text-shadow .3s ease;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 32px;
		}

		@media screen and (min-width : 1450px) {
			font-size   : 16px;
			font-weight : 400;
			line-height : 24px;
		}

		&:hover {
			text-shadow : 0 0 1px $black;
		}
	}
}

.header__menu {
	background-color : #FFDF34;
	height           : 100dvh;
	left: -100%;
	opacity: 0;
	overflow: hidden;
	position         : absolute;
	top              : -12px;
	transition       : visibility .3s ease-in, opacity .3s ease-in, left .3s ease;
	visibility: hidden;
	width            : 100%;
	z-index          : 100;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1450px) {
		background-color : transparent;
		height           : auto;
		left             : unset;
		opacity: 1;
		overflow: visible;
		position         : static;
		top              : unset;
		visibility: visible;
		width            : auto;
	}

	&.active {
		left: 0;
		opacity: 1;
		visibility: visible;
	}
}

.header__wrapper {
	align-items     : center;
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : space-between;
	overflow-x: hidden;
	overflow-y      : auto;
	padding         : 24px 24px;

	@media screen and (min-width : $break-table) {
		padding : 36px 24px 72px 24px;
	}

	@media screen and (min-width : 1450px) {
		padding : 0;
	}
}

.s-block {
	display : flex;
}

.s-block__item {
	align-items      : center;
	background-color : $primary;
	border-radius    : 100%;
	display          : flex;
	height           : 40px;
	justify-content  : center;
	overflow         : hidden;
	transition       : background-color .3s ease;
	width            : 40px;

	&:not(:first-child) {
		margin-left : 8px;
	}

	svg {
		path {
			transition : fill .3s ease;
		}
	}

	&:hover {
		background-color : black;

		svg {

			path {
				fill       : #FFDF34;
				transition : fill .3s ease;
			}
		}
	}
}

.work-time {
	margin-left : 24px;

	a {
		color       : $black;
		font-size   : 20px;
		font-weight : 800;
		line-height : 28px;
	}

	span {
		color       : $black;
		display     : block;
		font-size   : 12px;
		font-weight : 400;
		line-height : 16px;
	}
}


.burger-wrapper {
	align-items     : center;
	display         : flex;
	height          : 32px;
	justify-content : center;
	position        : relative;
	width           : 32px;
	z-index         : 100;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1450px) {
		display : none;
	}
}

.burger {
	background-color : transparent;
	border           : none;
	color            : $black;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 16px;
	padding          : 0;
	position         : relative;
	width            : 24px;


	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 2px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&::before {
		top : 0;
	}

	&::after {
		top : calc(100% - 2px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 2px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

	&--active {
		&::before {
			top        : 50%;
			transform  : rotate(45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		&::after {
			top        : 50%;
			transform  : rotate(-45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		.burger__line {
			transform  : scale(0);
			transition : transform 0.3s ease-in-out;
		}
	}
}

.header__button-desktop {
	display : none;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1450px) {
		display : flex;
	}
}
