.skills {
	background-color : $primary;
	padding          : 64px 0;

	@media screen and (min-width : $break-table) {
		padding : 96px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}

}

.skills__container {
	position : relative;
}

.skills__wave {
	position : absolute;
	right    : -228px;
	top      : 657px;
	z-index  : 1;
}

.skills__rhombus {
	position : absolute;
	right    : -206px;
	top      : 1347px;
	z-index  : 1;
}

.skills__lines {
	display  : none;
	height   : 120px;
	position : absolute;
	right    : 0;
	top      : 0;
	width    : 120px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		display : block;
		height  : 220px;
		right   : -24px;
		top     : -64px;
		width   : 220px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 0;
		height : auto;
		right  : -494px;
		top    : unset;
		width  : auto;
	}
}

.skills__dots {
	height   : 120px;
	position : absolute;
	right    : -35px;
	top      : -35px;
	width    : 120px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		display : none;
		height  : 240px;
		width   : 240px;
	}

	@media screen and (min-width : $break-desktop) {
		display : block;
		height  : auto;
		right   : -125px;
		top     : -69px;
		width   : auto;
	}

}

.skills__rectangle {
	bottom   : 331px;
	left     : -195px;
	position : absolute;
	z-index  : 1;
}

.skills__dots--middle {
	left     : -483px;
	position : absolute;
	top      : 788px;
	z-index  : 1;
}

.skills__box {
	margin-bottom : 44px;
	position      : relative;
	z-index       : 2;

	@media screen and (min-width : $break-table) {
		margin-bottom : 86px;
		max-width     : 626px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : none;
	}

}

.skills__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 0;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 72px;
	}
}

.skills-list {
	counter-reset : count;
}

.skills-list__item {
	align-items   : center;
	border-radius : 12px;
	display       : flex;
	overflow      : hidden;
	padding       : 80px 16px 32px 16px;
	position      : relative;

	@media screen and (min-width : $break-table) {
		padding : 160px 48px 68px 48px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 48px 48px 72px 100px;
	}

	&:not(:last-child) {
		margin-bottom : 16px;
	}

	&--lightblue {
		background-color : $light-blue;
	}

	&--green {
		background-color : $green;
	}

	&--pink {
		background-color : $pink;
	}

	&:before {
		color             : $black;
		content           : '0' counter(count);
		counter-increment : count;
		font-size         : 14px;
		font-weight       : 400;
		left              : 16px;
		line-height       : 16px;
		position          : absolute;
		top               : 16px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			left        : 48px;
			line-height : 32px;
			top         : 48px;
		}
	}

	&:nth-child(1) {
		.skills-dots {
			height   : 120px;
			left     : 85px;
			position : absolute;
			top      : -66px;
			width    : 120px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				height : 240px;
				left   : 141px;
				top    : -141px;
				width  : 240px;
			}

			@media screen and (min-width : $break-desktop) {
				height : auto;
				left   : 281px;
				top    : -109px;
				width  : auto;
			}
		}

		.skills-rhombus {
			bottom   : -7px;
			height   : 20px;
			left     : 69px;
			position : absolute;
			width    : 20px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom : 14px;
				height : auto;
				left   : 95px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {
				bottom : 20px;

				left   : 69px;

			}
		}

		.skills-line {
			bottom   : 11px;
			height   : 10px;
			position : absolute;
			right    : 23px;
			width    : 61px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom : 23px;
				height : auto;
				right  : 61px;
				width  : auto;

			}

			@media screen and (min-width : $break-desktop) {
				bottom : 25px;
				right  : 48px;
			}
		}
	}

	&:nth-child(2) {
		.skills-dots {
			bottom   : 0;
			height   : 120px;
			position : absolute;
			right    : 30px;
			width    : 120px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom : -25px;
				height : 210px;
				right  : 75px;
				width  : 210px;
			}

			@media screen and (min-width : $break-desktop) {
				bottom : -70px;
				height : auto;
				right  : 136px;
				width  : auto;
			}

		}

		.skills-rectangle {
			height    : 24px;
			left      : 101px;
			position  : absolute;
			top       : 17px;
			transform : rotate(45deg);
			width     : 24px;
			z-index   : 1;

			@media screen and (min-width : $break-table) {
				height : 40px;
				left   : 130px;
				top    : 51px;
				width  : 40px;
			}

			@media screen and (min-width : $break-desktop) {
				left : 341px;
				top  : 40px;
			}
		}
	}

	&:nth-child(3) {
		.skills-lines {
			height   : 120px;
			left     : -50px;
			position : absolute;
			top      : 50px;
			width    : 120px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				height : 240px;
				left   : -56px;
				top    : 101px;
				width  : 240px;
			}

			@media screen and (min-width : $break-desktop) {
				height : auto;
				left   : 262px;
				top    : 21px;
				width  : auto;
			}
		}

		.skills-line {
			bottom   : 12px;
			height   : 12px;
			position : absolute;
			right    : 55px;
			width    : 68px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom : 22px;
				height : auto;
				right  : 214px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {
				bottom : 25px;
				right  : 272px;
			}
		}

		.skills-rhombus {
			display  : none;
			position : absolute;
			right    : 54px;
			top      : -17px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				display : block;
				right   : 0;
				top     : -23px;
			}

			@media screen and (min-width : $break-desktop) {
				right : 54px;
				top   : -17px;
			}
		}
	}

	&:nth-child(4) {
		.skills-dots {
			bottom   : -28px;
			height   : 120px;
			position : absolute;
			right    : 48px;
			width    : 120px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom : -22px;
				height : 240px;
				right  : 178px;
				width  : 240px;
			}

			@media screen and (min-width : $break-desktop) {
				bottom : -20px;
				height : auto;
				right  : 307px;
				width  : auto;
			}
		}

		.skills-rectangle {
			height   : 20px;
			left     : -12px;
			position : absolute;
			top      : 55px;
			width    : 20px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				height : 40px;
				left   : -20px;
				top    : 158px;
				width  : 40px;
			}

			@media screen and (min-width : $break-desktop) {
				left : 286px;
				top  : 70px;
			}
		}
	}

	&:nth-child(5) {
		.skills-dots {
			height   : 120px;
			left     : 76px;
			position : absolute;
			top      : 28px;
			width    : 120px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				height : 240px;
				left   : 115px;
				top    : 62px;
				width  : 240px;
			}

			@media screen and (min-width : $break-desktop) {
				height : auto;
				left   : 325px;
				top    : -98px;
				width  : auto;
			}
		}

		.skills-rhombus {
			bottom   : 0;
			height   : 13px;
			left     : -7px;
			position : absolute;
			width    : 13px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom : 22px;
				height : 43px;
				left   : -12px;
				width  : 43px;
			}

			@media screen and (min-width : $break-desktop) {
				bottom  : 20px;
				display : block;
				height  : auto;
				left    : 69px;
				width   : auto;
			}
		}

		.skills-line {
			bottom   : 11px;
			height   : 10px;
			position : absolute;
			right    : 57px;
			width    : 61px;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				bottom : 32px;
				height : auto;
				right  : 48px;
				width  : auto;
			}

			@media screen and (min-width : $break-desktop) {
				bottom : 25px;
				right  : 48px;
			}
		}
	}
}

.skills-list__image {
	position : absolute;
	right    : 21px;
	top      : 42px;
	width    : 66px;
	z-index  : 3;

	img {

	}

	@media screen and (min-width : $break-table) {
		right : 56px;
		top   : 36px;
		width : 160px;
	}

	@media screen and (min-width : $break-desktop) {
		flex-shrink : 0;
		height      : auto;
		position    : static;
		width       : 222px;
	}
}

.skills-list__wrap {
	flex-grow : 1;
	padding   : 32px 16px 16px 16px;
	position  : relative;
	z-index   : 2;

	@media screen and (min-width : $break-table) {
		padding : 64px 32px 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 84px;
		padding     : 48px 48px;
	}
}

.skills-list__title {
	color         : $black;
	font-size     : 14px;
	font-weight   : 800;
	line-height   : 20px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.skills-list__text {
	color       : $black;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
