.main-blog {
	padding : 64px 0;

	@media screen and (min-width : $break-table) {
		padding : 96px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}
}

.main-blog__container {
	position : relative;
}

.main-blog__circle {
	bottom   : -50px;
	height   : 33px;
	position : absolute;
	right    : -17px;
	width    : 33px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 77px;
		height : auto;
		right  : -22px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 330px;
		right  : -211px;
	}

}

.main-blog__wave {
	height   : 17px;
	position : absolute;
	right    : -15px;
	top      : -9px;
	width    : 80px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : auto;
		right  : 144px;
		top    : 32px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {
		right : 333px;
		top   : 0;
	}
}

.main-blog__dots {
	left     : -483px;
	position : absolute;
	top      : 0;
	z-index  : 1;
}

.main-blog__rhombus {
	bottom   : -52px;
	left     : -10px;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		bottom : 181px;
		left   : -199px;
	}
}

.main-blog__wrapper {
	align-items     : flex-end;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 53px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 83px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main-blog__title {
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 0;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 72px;
	}
}

.main-blog__box {
	background-color : $black;
	color            : $white;
	max-width        : 565px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.blog-card {
	display : block;

	&:hover {
		.blog-card__img {
			border-radius : 12px;
			transition    : border-radius .3s ease;
		}
	}

	@media screen and (min-width : $break-table) {
		margin : 0 12.5px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 20px;
	}

	&__img {
		margin-bottom : 32px;
		overflow      : hidden;
		transition    : border-radius .3s ease;

		img {
			height     : 100%;
			max-width  : none;
			object-fit : cover;
			width      : 100%;
		}
	}

	&__date {
		color         : $black;
		display       : block;
		font-size     : 12px;
		font-weight   : 800;
		line-height   : 16px;
		margin-bottom : 8px;
		opacity       : 0.5;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 20px;
		}

		@media screen and (min-width : $break-desktop) {

		}

	}

	&__title {
		-webkit-box-orient : vertical;
		color              : $black;
		display            : -webkit-box;
		font-size          : 16px;
		font-weight        : 800;
		-webkit-line-clamp : 2;
		line-height        : 24px;
		overflow           : hidden;
		-o-text-overflow   : ellipsis;
		text-overflow      : ellipsis;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.main-blog__slider {
	.b-btn--outline {
		width : 100%;
	}
}

.swiper--blog {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin        : 0 -12.5px;
		margin-bottom : 36px;
	}

	@media screen and (min-width : $break-desktop) {
		margin        : 0 -20px;
		margin-bottom : 48px;
	}

	.swiper-slide {
		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 33.33333%;
		}
	}
}
