
.box-item {
	background-color : $white;
	border-radius    : 12px 12px 12px 0;
	position         : relative;


	&:before {
		background-color : inherit;
		border           : inherit;
		bottom           : -94px;
		clip-path        : path('M8.06329 15.7745L26 0H0V12.5706C0 16.4531 5.02148 18.452 8.06329 15.7745Z');
		content          : "";
		display          : block;
		height           : 94px;
		left             : 0;
		position         : absolute;
		width            : 94px;

		@media screen and (min-width : $break-table) {
			clip-path : path('M14.5769 31.549L47.001 0H0.000976562V25.1413C0.000976562 32.9062 9.07827 36.9041 14.5769 31.549Z');
		}
	}

}

.box-item--medium {
	padding : 24px 24px;

	&:before {
		bottom    : -60px;
		clip-path : path('M8.06329 15.7745L26 0H0V12.5706C0 16.4531 5.02148 18.452 8.06329 15.7745Z');
		height    : 60px;
		width     : 60px;
	}
}

.box-item--small {
	font-size   : 16px;
	font-weight : 800;
	line-height : 24px;
	padding     : 8px 10px;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 32px;
	}

	&:before {
		background-color : inherit;
		bottom           : -9px;
		clip-path        : path('M0 4.82761V0H14L4.85347 7.18656C2.88508 8.73315 0 7.3309 0 4.82761Z');
		height           : 10px;
		width            : 14px;
	}
}


.box-item--default {
	font-size   : 20px;
	font-weight : 800;
	line-height : 28px;
	padding     : 16px 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 42px;
		padding     : 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 56px;
	}


	&:before {
		background-color : inherit;
		bottom           : -17px;
		clip-path        : path('M8.06329 15.7745L26 0H0V12.5706C0 16.4531 5.02148 18.452 8.06329 15.7745Z');
		height           : 18px;
		width            : 26px;

		@media screen and (min-width : $break-table) {
			bottom    : -35px;
			clip-path : path('M14.5769 31.549L47.001 0H0.000976562V25.1413C0.000976562 32.9062 9.07827 36.9041 14.5769 31.549Z');
			height    : 36px;
			width     : 47px;
		}
	}
}

.box-item--right {
	border-radius : 12px 12px 0 12px;
	padding       : 16px 16px;

	@media screen and (min-width : $break-table) {
		padding : 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		background-color : inherit;
		bottom           : -10px;
		clip-path        : path('M14 4.828V0H0l9.147 7.187C11.115 8.733 14 7.33 14 4.827Z');
		height           : 10px;
		left             : unset;
		right            : 0;
		width            : 14px;

		@media screen and (min-width : $break-table) {
			bottom    : -35px;
			clip-path : path('M32.424 31.549 0 0h47v25.141c0 7.765-9.077 11.763-14.576 6.408Z');
			height    : 36px;
			left      : unset;
			right     : 0;
			width     : 47px;
		}
	}
}

.box-item--top {
	border-radius : 0 12px 12px 12px;
	padding       : 16px 16px;

	@media screen and (min-width : $break-table) {
		padding : 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		background-color : inherit;
		clip-path        : path('M8.06329 1.22552L26 17H0V4.42936C0 0.546911 5.02148 -1.45203 8.06329 1.22552Z');
		height           : 21px;
		left             : 0;
		top              : -17px;
		width            : 28px;

		@media screen and (min-width : $break-table) {
			clip-path : path('M14.5759 2.45104L47 34H0V8.85872C0 1.09382 9.07729 -2.90406 14.5759 2.45104Z');
			height    : 36px;
			left      : 0;
			top       : -34px;
			width     : 47px;
		}
	}
}

.box-item--black {
	background-color : $black;
	color            : $white;
}

.box-item--yellow {
	background-color : $primary;
	color            : $black;
}

.box-item--green {
	background-color : $green;
	color            : $black;
}

.box-item--lightblue {
	background-color : $light-blue;
	color            : $black;
}

.box-item--blue {
	background-color : $blue;
	color            : $white;
}

.box-item--pink {
	background-color : $pink;
	color            : $black;
}
