.hero {
	background-color : $primary;
	overflow         : hidden;
	padding-top      : 86px;

	@media screen and (min-width : $break-table) {
		padding-top : 132px;
	}

	@media screen and (min-width : 1360px) {
		padding-top : 156px;
	}
}

.hero__container {
	max-width : 1435px;
	position  : relative;
}


.hero__wrapper {
	display        : flex;
	flex-direction : column;
	position       : relative;
	z-index        : 2;

	@media screen and (min-width : $break-table) {
		padding : 0 30px;
	}

	@media screen and (min-width : 1360px) {
		flex-direction : row;
		padding        : 0;
	}
}

.hero__item {
	background-color : $white;
	margin-bottom    : 40px;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 66px;
		padding       : 36px 36px;
	}

	@media screen and (min-width : 1360px) {
		margin-bottom : 60px;
		padding       : 48px 48px;
	}
}

.hero__main {

	margin-bottom : 36px;

	.b-btn--black {
		font-size       : 14px;
		justify-content : center;
		min-width       : 291px;
	}


	@media screen and (min-width : $break-table) {
		margin-bottom : 0;

		.b-btn--black {
			font-size : 20px;
			width     : auto;
		}
	}

	@media screen and (min-width : 1360px) {
		margin-left : 100px;
		margin-top  : 75px;
		max-width   : 779px;
	}
}

.hero__img {
	align-items     : center;
	display         : flex;
	flex-direction  : column;
	flex-shrink     : 0;
	justify-content : center;

	@media screen and (min-width : $break-table) {
		align-items : end;
		margin-top  : -170px;
	}

	@media screen and (min-width : 1360px) {
		margin-top : 0;
	}

}

.hero__image {
	width : 88%;

	@media screen and (min-width : $break-table) {
		position : relative;
		width    : 62%;
	}

	@media screen and (min-width : 768px) {
		right : unset;
	}

	@media screen and (min-width : 1360px) {
		left  : -132px;
		width : 100%;
	}
}

.hero__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : 1360px) {
		font-size   : 48px;
		font-weight : 800;
		line-height : 56px;
		max-width   : 683px;
	}
}

.hero__text {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;
	}

	@media screen and (min-width : 1360px) {
		max-width : 546px;
	}
}


.box-item--hero {
	&:before {
		background-color : inherit;
		bottom           : -54px;
		clip-path        : path('M8.06329 15.7745L26 0H0V12.5706C0 16.4531 5.02148 18.452 8.06329 15.7745Z');
		height           : 54px;
		width            : 54px;

		@media screen and (min-width : $break-table) {
			bottom    : -94px;
			clip-path : path('M14.5769 31.549L47.001 0H0.000976562V25.1413C0.000976562 32.9062 9.07827 36.9041 14.5769 31.549Z');
			height    : 94px;
			width     : 94px;
		}
	}
}


.hero__parallax {
	height   : 100%;
	left     : 0;
	position : absolute;
	top      : 0;
	width    : 100%;
	z-index  : 1;
}

.hero__dots {
	left     : -42px !important;
	position : relative !important;
	top      : -13px !important;
	width    : 120px;

	@media screen and (min-width : $break-table) {
		left  : -80px !important;
		top   : -21px !important;
		width : 240px;
	}

	@media screen and (min-width : 1360px) {
		left  : 0 !important;
		top   : 0 !important;
		width : 316px;
	}
}


.hero__lines {
	bottom   : 0 !important;
	left     : unset !important;
	position : absolute;
	right    : -35px !important;
	top      : unset !important;
	width    : 120px;

	@media screen and (min-width : $break-table) {
		bottom : -41px !important;
		right  : 11px !important;
		width  : 260px;
	}

	@media screen and (min-width : 1360px) {
		bottom : -20px !important;
		right  : 0 !important;
	}
}


.hero__line {
	left     : unset !important;
	position : absolute;
	right    : 0 !important;
	top      : 20px !important;

	@media screen and (min-width : $break-table) {
		left  : unset !important;
		right : 20px !important;
	}

	@media screen and (min-width : 1360px) {
		left  : unset !important;
		right : 0 !important;
		top   : 0 !important;
	}
}

.hero__wave {
	left      : -26px !important;
	position  : absolute;
	top       : 60% !important;
	transform : rotate(180deg);
	width     : 79px;

	@media screen and (min-width : $break-table) {
		left  : -45px !important;
		top   : 70% !important;
		width : 150px;
	}

	@media screen and (min-width : 1360px) {
		left      : 50% !important;
		top       : 0 !important;
		transform : translateX(-50%) rotate(360deg);
	}
}

.hero__rectangle {
	left     : unset !important;
	position : absolute;
	right    : 41px !important;
	top      : 414px !important;
	width    : 18px;

	@media screen and (min-width : $break-table) {
		left  : unset !important;
		right : 44px !important;
		top   : 370px !important;
		width : 48px;
	}

	@media screen and (min-width : 1360px) {
		left  : unset !important;
		right : 0 !important;
		top   : 200px !important;
	}

}

.hero__rhombus {
	bottom   : 21px !important;
	left     : 61px !important;
	position : absolute;
	top      : unset !important;
	width    : 20px;

	@media screen and (min-width : $break-table) {
		bottom : 67px !important;
		left   : 154px !important;
		top    : unset !important;
		width  : 48px;
	}

	@media screen and (min-width : 1360px) {
		bottom : 90px !important;
		left   : 45% !important;
		top    : unset !important;
		width  : 40px;
	}
}
