.disable-scroll {
	height   : 100vh;
	left     : 0;
	overflow : hidden;
	position : fixed;
	top      : 0;
	width    : 100%;
}


.graph-modal {
	background-color : rgba(0, 0, 0, 0.7);
	bottom           : 0;
	cursor           : pointer;
	left             : 0;
	opacity          : 0;
	overflow-x       : hidden;
	overflow-y       : auto;
	position         : fixed;
	right            : 0;
	text-align       : center;
	top              : 0;
	transition       : opacity .3s ease, visibility .3s ease;
	visibility       : hidden;
	z-index          : 1000;
}

.graph-modal:before {
	content        : '';
	display        : inline-block;
	height         : 100%;
	vertical-align : middle;
}

.graph-modal__container {
	background-color : $light-blue;
	border-radius    : 12px;
	cursor           : default;
	display          : none;
	margin           : 50px auto;
	padding          : 48px 14px 30px 14px;
	position         : relative;
	text-align       : left;
	vertical-align   : middle;
	width            : 320px;

	@media screen and (min-width : $break-table) {
		padding : 48px 48px 82px 48px;
		width   : 600px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.graph-modal__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 32px;
		line-height   : 42px;
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 56px;
	}
}

.graph-modal__text {
	color         : $black;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.graph-modal__info {
	display       : flex;
	margin-bottom : 34px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 36px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	li {
		&:not(:first-child) {
			margin-left : 24px;
		}

		.info-top {
			color         : $black;
			display       : block;
			font-size     : 12px;
			font-weight   : 400;
			line-height   : 16px;
			margin-bottom : 8px;

			@media screen and (min-width : $break-table) {
				font-size   : 14px;
				line-height : 20px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		.info-bottom {
			color       : $black;
			display     : block;
			font-size   : 16px;
			font-weight : 800;
			line-height : 24px;

			@media screen and (min-width : $break-table) {
				font-size   : 24px;
				line-height : 32px;
			}

			@media screen and (min-width : $break-desktop) {

			}

		}
	}
}

.graph-modal__content {
	padding : 24px 24px;
	@media screen and (min-width : $break-table) {
		padding : 36px 36px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.form--modal {
	.form__body {
		margin-bottom : 24px;
	}

	.b-btn--black {
		font-size   : 14px;
		line-height : 20px;
		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 32px;
			padding     : 16px 51px;
			width       : auto;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

}

@media (max-width : 1024px) {
	.graph-modal__container {
		width : 90%;
	}
}

.graph-modal__close {
	background-color : transparent;
	background-image : url("../images/icon/close.svg");
	background-size  : 24px;
	border           : none;
	cursor           : pointer;
	height           : 24px;
	padding          : 0;
	position         : absolute;
	right            : 14px;
	top              : 14px;
	width            : 24px;

	@media screen and (min-width : $break-table) {
		background-size : 32px;
		height          : 32px;
		right           : 16px;
		top             : 16px;
		width           : 32px;
	}
}

.graph-modal.is-open {
	opacity    : 1;
	transition : opacity .2s ease, visibility .2s ease;
	visibility : visible;
}

.graph-modal__container.graph-modal-open {
	display : inline-block;
}

.fade {
	opacity : 0;
}

.fade,
.fade.animate-open {
	transition : opacity .2s;
}

.fade.animate-open {
	opacity : 1;
}

.fadeInUp {
	opacity   : 0;
	transform : translateY(-100px);
}

.fadeInUp,
.fadeInUp.animate-open {
	transition : transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
	opacity   : 1;
	transform : translateY(0);
}
