.faq {
	background-color : $primary;
	overflow         : hidden;
	padding-bottom   : 91px;
	padding-top      : 64px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 130px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 150px;
		padding-top    : 200px;
	}
}

.faq__container {
	position : relative;
}

.faq__lines {
	left     : -499px;
	position : absolute;
	top      : 377px;
	z-index  : 1;
}

.faq__rhombus {
	left     : 370px;
	position : absolute;
	top      : -140px;
	z-index  : 1;
}

.faq__wave {
	display  : none;
	position : absolute;
	right    : -36px;
	top      : 52px;
	z-index  : 1;

	@media screen and (min-width : $break-desktop) {
		display : block;
	}

}

.faq__dots {
	position : absolute;
	right    : -248px;
	top      : -348px;
	z-index  : 1;
}

.faq__rectangle {
	position : absolute;
	right    : -230px;
	top      : 658px;
	z-index  : 1;
}


.faq__wrap {
	margin-bottom : 24px;
	position      : relative;

	@media screen and (min-width : $break-table) {
		align-items   : center;
		display       : flex;
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 70px;
	}
}

.faq__box {
	background-color : $black;
	color            : $white;
	margin-bottom    : 30px;
	max-width        : 74px;
	text-transform   : uppercase;

	@media screen and (min-width : $break-table) {
		max-width : none;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.faq__title {
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 0;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 72px;
	}
}

.faq__text {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;
	position    : relative;
	z-index     : 2;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;
		margin-left : 48px;
		max-width   : 256px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 82px;
		max-width   : 377px;
	}
}

@keyframes bublesDesc {
	0% {
		transform : translatey(0px);
	}
	50% {
		transform : translatey(-60px);
	}
	100% {
		transform : translatey(0px);
	}
}

.faq__bubbles {
	pointer-events : none;
	position       : absolute;
	right          : -73px;
	top            : -49px;
	width          : 305px;
	z-index        : 1;

	@media screen and (min-width : $break-table) {
		right : -129px;
		top   : -42px;
		width : 497px;
	}

	@media screen and (min-width : $break-desktop) {
		animation : bublesDesc 2s ease-in-out infinite;
		right     : 8px;
		top       : -126px;
		width     : auto;
	}
}

.faq__wrapper {
	counter-reset : count;
}

.accordion-mini {
	&:not(:last-child) {
		margin-bottom : 12px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 16px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.accordion-mini__control {
	align-items      : center;
	background-color : $white;
	border           : none;
	border-radius    : 12px;
	color            : $black;
	display          : flex;
	justify-content  : space-between;
	padding          : 16px 16px;
	position         : relative;
	transition       : padding .3s ease;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		padding : 48px 40px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 36px 40px;
	}

	&:hover {
		.accordion-mini__icon {
			background-color : $black;

			svg {
				fill       : $white;
				transition : fill .3s ease, stroke .3s ease, transform .3s ease;

				path {
					fill   : $white;
					stroke : $white;
				}
			}

		}
	}

	img {
		height     : 32px;
		transition : transform .3s ease;
		width      : 32px;

		@media screen and (min-width : $break-table) {
			height : 40px;
			width  : 40px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.accordion-mini__name {
	display     : block;
	font-size   : 14px;
	font-weight : 800;
	line-height : 20px;
	position    : relative;
	text-align  : left;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		color             : #000;
		content           : counter(count) '.';
		counter-increment : count;
		display           : inline-block;
		font-size         : 14px;
		font-weight       : 800;
		line-height       : 20px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.accordion-mini__icon {
	align-items      : center;
	background-color : transparent;
	border-radius    : 100%;
	display          : flex;
	flex-shrink      : 0;
	height           : 32px;
	justify-content  : center;
	margin-left      : 10px;
	transition       : background-color .3s ease;
	width            : 32px;

	@media screen and (min-width : $break-table) {
		height : 64px;
		width  : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	svg {
		height : 20px;
		width  : 20px;

		@media screen and (min-width : $break-table) {
			height : 40px;
			width  : 40px;
		}

		path {
			transition : fill .3s ease, stroke .3s ease;
		}
	}

}

.accordion-mini__content {
	background-color : $white;
	border-radius    : 0 0 12px 12px;
	box-sizing       : content-box;
	color            : $black;
	font-size        : 24px;
	font-weight      : 800;
	line-height      : 32px;
	max-height       : 0;
	opacity          : 0;
	overflow         : hidden;
	padding          : 0 16px 0 16px;
	transition       : all .3s ease;
	will-change      : max-height;

	p {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-table) {
		padding : 0 40px 0 40px;
		p {
			font-size   : 20px;
			font-weight : 400;
			line-height : 32px;
			max-width   : 627px;
		}
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.accordion-mini.open .accordion-mini__control {
	border-radius  : 12px 12px 0 0;
	padding-bottom : 24px;
	transition     : padding .3s ease;

	.accordion-mini__icon {
		background-color : $black;

		svg {
			transform : rotate(180deg);

			path {
				fill   : $white;
				stroke : $white;
			}
		}
	}
}

.accordion-mini.open .accordion-mini__content {
	background-color : $white;
	opacity          : 1;
	padding          : 0 16px 16px 16px;

	@media screen and (min-width : $break-table) {
		padding : 0 40px 48px 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
