.questions {
	background-color : $primary;
	padding-bottom   : 64px;
	padding-top      : 0;

	@media screen and (min-width : $break-table) {
		padding-bottom : 96px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}
}

.questions__container {
	position : relative;

	.questions__wave {
		display  : none;
		left     : 484px;
		position : absolute;
		top      : -131px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : block;
		}
	}

	.questions__rectangle {
		display : none;
		@media screen and (min-width : $break-table) {
			display  : block;
			left     : -20px;
			position : absolute;
			top      : 52px;
			z-index  : 1;
		}

		@media screen and (min-width : $break-desktop) {
			left : -199px;
			top  : 133px;
		}
	}

	.questions__rhombus {
		height   : 20px;
		left     : 89px;
		position : absolute;
		top      : -55px;
		width    : 20px;
		z-index  : 1;

		@media screen and (min-width : $break-table) {
			bottom : 320px;
			height : auto;
			left   : unset;
			right  : 27px;
			top    : unset;
			width  : auto;
		}

		@media screen and (min-width : $break-desktop) {
			bottom : 319px;
			right  : -205px;
		}
	}

	.questions__dots--middle {
		bottom   : 72px;
		left     : -497px;
		position : absolute;
		z-index  : 1;
	}
}

.questions__box {
	background-color : $black;
	color            : $white;
	display          : flex;
	margin-bottom    : 59px;
	position         : relative;
	z-index          : 2;

	@media screen and (min-width : 531px) {
		margin-bottom : 109px;
	}

	@media screen and (min-width : $break-table) {
		margin-bottom : 94px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 189px;
	}

	.questions__dots {
		height   : 154px;
		position : absolute;
		right    : -88px;
		top      : -56px;
		width    : 154px;
		z-index  : -1;

		@media screen and (min-width : $break-table) {
			height : 210px;
			right  : -32px;
			top    : -44px;
			width  : 210px;
		}

		@media screen and (min-width : $break-desktop) {
			height : auto;
			right  : -168px;
			top    : -137px;
			width  : auto;
		}
	}
}

.questions__wrap {
	max-width : 145px;

	@media screen and (min-width : 531px) {
		max-width : 345px;
	}

	@media screen and (min-width : $break-table) {
		max-width : 357px;
	}

	@media screen and (min-width : $break-desktop) {

		max-width : 630px;
	}
}

.questions__title {
	color         : $white;
	font-size     : 20px;
	line-height   : 28px;
	margin-bottom : 10px;


	@media screen and (min-width : 450px) {
		max-width : 345px;
	}

	@media screen and (min-width : $break-table) {
		font-size     : 40px;
		line-height   : 48px;
		margin-bottom : 24px;
		max-width     : 357px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 72px;
		max-width   : 630px;
	}
}

.questions__text {
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;


	@media screen and (min-width : 450px) {

	}

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.questions__image {
	flex-shrink : 0;
	height      : 358px;
	position    : absolute;
	right       : -48px;
	top         : -100px;
	width       : 180px;

	@media screen and (min-width : $break-table) {
		height : 460px;
		right  : 16px;
		top    : -126px;
		width  : 232px;
	}

	@media screen and (min-width : $break-desktop) {
		right : 84px;
		top   : -228px;
		width : 358px;
	}
}

@keyframes bublesDesc {
	0% {
		transform : translatey(0px);
	}
	50% {
		transform : translatey(-60px);
	}
	100% {
		transform : translatey(0px);
	}
}

.questions__bubbles {
	display  : none;
	height   : 181px;
	left     : -202px;
	position : absolute;
	top      : -228px;
	width    : 511px;
	z-index  : 1;

	@media screen and (min-width : $break-desktop) {
		animation : bublesDesc 3s ease-in-out infinite;
		display   : block;
	}
}

.question--section {

	@media screen and (min-width : $break-table) {
		padding-top : 130px;
	}
	@media screen and (min-width : $break-desktop) {
		padding-top : 280px;
	}

}
