.join {
	padding : 64px 0;

	@media screen and (min-width : $break-table) {
		padding : 96px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}
}

.join__wrapper {

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.join__wrap {
	margin-bottom : 48px;

	.b-btn--black {
		font-size   : 14px;
		line-height : 20px;
		width       : 100%;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 32px;
			width       : auto;
		}
	}

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 367px;
		padding-right : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 488px;
	}
}

.join__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 800;
	line-height   : 28px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 42px;
		max-width   : 400px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 40px;
		line-height : 48px;
	}
}

.join__text {
	color         : $black;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.join__image {
	position : relative;

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.join-lines {
	bottom   : 40px;
	height   : 160px;
	position : absolute;
	right    : -62px;
	width    : 160px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		bottom : -32px;
		height : 180px;
		right  : -47px;
		width  : 180px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : -52px;
		height : auto;
		right  : -93px;
		width  : auto;
	}
}


.join-circle {
	height   : 26px;
	left     : -15px;
	position : absolute;
	top      : 74px;
	width    : 26px;
	z-index  : -1;

	@media screen and (min-width : $break-table) {
		height : 30px;
		width  : 30px;
	}

	@media screen and (min-width : $break-desktop) {
		height : auto;
		left   : -26px;
		top    : 130px;
		width  : auto;
	}
}
